<template>
    <div class="">

    </div>
</template>

<script>
import { confirm_account } from '@/api/api';
export default {
    name: 'ConfirmAccount',
    components: {
    },
    created() {
        confirm_account(this.$route.params.confirmation_token).then((response) => {
            if (response.status === 0) {
                alert('Account confirmed successfully');
                this.$router.replace({ name: 'HomePage' });
            } else {
                alert('Invalid token');
                this.$router.replace({ name: 'HomePage' });
            }
        });
    }
}
</script>

