<template>
  <div class="debug-output">Logged in: {{ greencycle_loggedin }} | User ID: {{ greencycle_user_id }} |
    Username: {{ greencycle_username }} | User type: {{ greencycle_description }} | Session ID: {{
      greencycle_sessionid }}</div>
  <router-view />
</template>

<script>
import { get_session_user } from './api/api';
export default {
  created() {
    get_session_user(localStorage.getItem('session_id')).then((response) => {
      if (response.status === 0) {
        this.greencycle_loggedin.value = 1;
        this.greencycle_user_id.value = response.user_id;
        this.greencycle_username.value = response.username;
        this.greencycle_description.value = response.description;
        this.greencycle_sessionid.value = localStorage.getItem('session_id');
      }
    });
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

#app {
  /* padding-inline: 15%; */
  font-family: 'Inter';
}

body {
  background-color: #202020;
}

.debug-output {
  color: white;
}
</style>
