<template>
    <div class="">
        <div class="login-container">
            <div class="generic-container-0">
                <div class="header">
                    <div class="header-logo">
                        <img src="@/assets/greencycle-logo.png" alt="">
                    </div>
                    <div class="header-text">
                        RESET PASSWORD
                    </div>
                </div>
                <div class="main-logo center">
                    <img src="@/assets/greencycle-logo.png" alt="">
                </div>
                <div class="main-text center">
                    where waste finds purpose,<br>
                    and sustainability takes flight
                </div>
            </div>
            <div class="generic-container-1">
                <div class="form-container">
                    <div class="form-field">
                        <img src="@/assets/user-email-icon.png" alt="">
                        <input @keyup.enter="submit" ref="form_password" type="password" placeholder="New password">
                    </div>
                    <div class="form-field">
                        <img src="@/assets/user-email-icon.png" alt="">
                        <input @keyup.enter="submit" ref="form_confirm_password" type="password" placeholder="Confirm new password">
                    </div>
                    <div style='display: flex; gap: 1rem;'>
                        <input ref="show_password_checkbox" @click="show_password" type="checkbox" id="show_password">
                        <label for="show_password">Show Password?</label>
                    </div>
                    <div class="form-submit center">
                        <button @click="submit">SUBMIT</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { reset_password } from '@/api/api';
export default {
    name: 'ResetPassword',
    components: {
    },
    methods: {
        show_password: function () {
            if (this.$refs.show_password_checkbox.checked) {
                this.$refs.form_password.type = 'text';
                this.$refs.form_confirm_password.type = 'text';
            } else {
                this.$refs.form_password.type = 'password';
                this.$refs.form_confirm_password.type = 'password';
            }
        },
        submit: function () {
            reset_password(this.$route.params.password_reset_token, this.$refs.form_password.value).then((response) => {
                if (response.status === 0) {
                    alert('Password reset successfully');
                    this.$router.replace({ name: 'HomePage' });
                } else if (response.status === -1) {
                    alert('Invalid token');
                    this.$router.replace({ name: 'HomePage' });
                } else if (response.status === -2) {
                    alert('Please provide a password');
                }
            });
        }
    },
    created() {
        reset_password(this.$route.params.password_reset_token, '').then((response) => {
            if (response.status !== -2) {
                alert('Invalid token');
                this.$router.replace({ name: 'HomePage' });
            }
        });
    }
}
</script>

<style scoped>
.center {
    text-align: center;
}

.form-container {
    background-color: #F1F1F1;
    border-radius: 1rem;
    margin: 1rem;
    padding: 1rem;
}

.form-container>* {
    margin: 1rem 0 0 0;
}

.form-field {
    display: flex;
    align-items: center;
}

.form-field img {
    height: 2.5rem;
    margin-right: 1rem;
}

.form-field input {
    font-size: 2rem;
}

.form-submit button {
    background-color: #86A37C;
    border: none;
    border-radius: 2rem;
    color: white;
    cursor: pointer;
    font-family: Inter;
    font-size: 2rem;
    font-weight: 800;
    padding: 0.5rem 3rem 0.5rem 3rem;
}

.generic-container-0 {
    display: flex;
    flex: 1;
    flex-direction: column;
}

.generic-container-1 {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header {
    display: flex;
    align-items: center;
}

.header-logo img {
    height: 9rem;
}

.header-text {
    color: #3C5E55;
    font-size: 2rem;
    font-weight: 800;
}

.login-container {
    display: flex;
    font-family: 'Inter';
    isolation: isolate;
    padding-bottom: 10rem;
    position: relative;
}

.login-container::before {
    content: '';
    background-image: url('@/assets/tree-bg.jpg');
    background-size: cover;
    position: absolute;
    inset: 0;
    z-index: -1;
    opacity: 0.5;
}

.main-logo img {
    height: 22rem;
}

.main-text {
    color: white;
    font-size: 1.5rem;
    font-style: italic;
    font-weight: 800;
    text-shadow:
        -2px 2px 1px #3C5E55,
        2px 2px 1px #3C5E55,
        2px -2px 1px #3C5E55,
        -2px -2px 1px #3C5E55,
        -2px 0px 1px #3C5E55,
        2px 0px 1px #3C5E55,
        0px 2px 1px #3C5E55,
        0px 2px 1px #3C5E55;
}
</style>