import { createRouter, createWebHistory } from 'vue-router';
import CharraView from '@/views/workspaces/CharraView.vue';
import VeniceView from '@/views/workspaces/VeniceView.vue';
import ErastoView from '@/views/workspaces/ErastoView.vue';
import RonView from '@/views/workspaces/RonView.vue';
import HomePage from '@/views/HomePage.vue';
import ApiTest from '@/views/ApiTest.vue';

/** About */
import AboutUs from '@/views/About/AboutUs.vue';
import BeOurLogistic from '@/views/About/BeOurLogistic.vue';
import CategoriesList from '@/views/About/CategoriesList.vue';
import FrequentlyAskPage from '@/views/About/FrequentlyAskPage.vue';
import PrivacyPolicyPage from '@/views/About/PrivacyPolicyPage.vue';
import TermsOfUsePage from '@/views/About/TermsOfUsePage.vue';

/** Authentication */
import ConfirmAccount from '@/views/Authentication/ConfirmAccount.vue';
import ForgotPassword from '@/views/Authentication/ForgotPassword.vue';
import LogIn from '@/views/Authentication/LogIn.vue';
import NewLogin from '@/views/Authentication/NewLogin.vue';
import ResetPassword from '@/views/Authentication/ResetPassword.vue';
import SignUp from '@/views/Authentication/SignUp.vue';

/** Dashboard*/
import AdminDashboard from '@/views/Dashboard/AdminDashboard.vue';
import BuyerDashboard from '@/views/Dashboard/BuyerDashboard.vue';
import SellerDashboard from '@/views/Dashboard/SellerDashboard.vue';

/** Product */
import CheckoutPage from '@/views/Product/CheckoutPage.vue';
import ProductDetailPage from '@/views/Product/ProductDetailPage.vue';
import ShoppingCartPage from '@/views/Product/ShoppingCartPage.vue';
import SuccessPage from '@/views/Product/SuccessPage.vue';

/** Profile */
import BuyerEditProfile from '@/views/Profile/BuyerEditProfile.vue';
import UserProfilePage from '@/views/Profile/UserProfilePage.vue';

/** Newsfeed */
import Newsfeed from '@/views/NewsfeedRon.vue';
import AddPostComponent from '@/components/AddPostComponent.vue';

const routes = [
  {
    path: '/',
    name : 'HomePage',
    component: HomePage
  },
  {
    path: '/charra',
    name: 'Charra',
    component: CharraView
  },
  {
    path: '/venice',
    name: 'Venice',
    component: VeniceView
  },
  {
    path: '/erasto',
    name: 'Erasto',
    component: ErastoView
  },
  {
    path: '/ron',
    name: 'Ron',
    component: RonView
  },
  { /** About */
    path: '/about/AboutUs',
    name: 'AboutUs',
    component: AboutUs
  },
  { /** About */
    path: '/about/BeOurLogistic',
    name: 'BeOurLogistic',
    component: BeOurLogistic
  },
  { /** About */
    path: '/about/CategoriesList',
    name: 'CategoriesList',
    component: CategoriesList
  },
  { /** About */
    path: '/about/FrequentlyAskPage',
    name: 'FrequentlyAskPage',
    component: FrequentlyAskPage
  },
  { /** About */
    path: '/about/PrivacyPolicyPage',
    name: 'PrivacyPolicyPage',
    component: PrivacyPolicyPage
  },
  { /** About */
    path: '/about/TermsOfUsePage',
    name: 'TermsOfUsePage',
    component: TermsOfUsePage
  },
  { /** Authentication */
    path: '/auth/ConfirmAccount/:confirmation_token',
    name: 'ConfirmAccount',
    component: ConfirmAccount
  },
  { /** Authentication */
    path: '/auth/ForgotPassword',
    name: 'ForgotPassword',
    component: ForgotPassword
  },
  { /** Authentication */
    path: '/auth/LogIn',
    name: 'LogIn',
    component: LogIn
  },
  { /** Authentication */
    path: '/auth/ResetPassword/:password_reset_token',
    name: 'ResetPassword',
    component: ResetPassword
  },
  { /** Authentication */
    path: '/auth/NewLogin',
    name: 'NewLogin',
    component: NewLogin
  },
  { /** Authentication */
    path: '/auth/SignUp',
    name: 'SignUp',
    component: SignUp
  },
  { /** Dashboard*/
    path: '/dashboard/AdminDashboard',
    name: 'AdminDashboard',
    component: AdminDashboard
  },
  { /** Dashboard*/
    path: '/dashboard/BuyerDashboard',
    name: 'BuyerDashboard',
    component: BuyerDashboard
  },
  { /** Dashboard*/
    path: '/dashboard/SellerDashboard',
    name: 'SellerDashboard',
    component: SellerDashboard
  },
  { /** Product */
    path: '/product/CheckoutPage/:id',
    name: 'CheckoutPage',
    component: CheckoutPage,
  },
  { /** Product */
    path: '/product/ProductDetailPage/:product_id',
    name: 'ProductDetailPage',
    component: ProductDetailPage
  },
  { /** Product */
    path: '/product/ShoppingCartPage',
    name: 'ShoppingCartPage',
    component: ShoppingCartPage
  },
  { /** Product */
    path: '/product/SuccessPage',
    name: 'SuccessPage',
    component: SuccessPage
  },
  { /** Profile */
    path: '/profile/BuyerEditProfile',
    name: 'BuyerEditProfile',
    component: BuyerEditProfile
  },
  { /** Profile */
    path: '/profile/UserProfilePage',
    name: 'UserProfilePage',
    component: UserProfilePage
  },
  { /** Newsfeed */
    path: '/newsfeed',
    name: 'Newsfeed',
    component: Newsfeed
  },
  { /** Newsfeed */
    path: '/addpost',
    name: 'AddPostComponent',
    component: AddPostComponent
  },
  {
    path: '/api',
    name: 'ApiTest',
    component: ApiTest
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
