<template>
    <div>
        <div class="component-window">
            <AdminViewProductInfoComponent v-if="showModal" :item_info="viewedItem" @close-modal="close_modal" />
            <div class="window-header">
                <div class="search-bar">
                    <input class="search" type="text" placeholder="Search...">
                    <button type="submit" class="search-btn">
                        <i class="fa fa-search"></i>
                    </button>
                </div>
                <div class="dropdown">
                    <button class="dropbtn" @click="toggleDropdown">{{ selectedOption || 'Category' }} &#9660;</button>
                    <div class="dropdown-content" :class="{ show: isDropdownOpen }">
                        <a @click="selectOption('Industrial')">Industrial</a>
                        <a @click="selectOption('Commercial')">Commercial</a>
                        <a @click="selectOption('Domestic')">Domestic</a>
                        <a @click="selectOption('Agricultural')">Agricultural</a>
                        <a @click="selectOption('Chemical')">Chemical</a>
                    </div>
                </div>
                <div class="dropdown">
                    <button class="dropbtn" @click="toggleAnotherDropdown">{{ selectedAnotherOption || 'Last added' }}
                        &#9660;</button>
                    <div class="dropdown-content" :class="{ show: isAnotherDropdownOpen }">
                        <a @click="selectAnotherOption('Option A')">Option A</a>
                        <a @click="selectAnotherOption('Option B')">Option B</a>
                        <a @click="selectAnotherOption('Option C')">Option C</a>
                        <!-- Add more options as needed -->
                    </div>
                </div>
            </div>
            <div class="products">
                <DashboardProductCardComponent v-for="value, key in items" :key="key" :item_key="key" :item_info="value"
                    :is_pending="false" :is_admin="true" @view-product="(item_key) => view_product_handler(item_key)" @delete-product="(item_key) => delete_product_handler(item_key)" />
            </div>

        </div>
    </div>
</template>

<script>
import DashboardProductCardComponent from '@/components/Dashboard/DashboardProductCardComponent.vue';
import { delete_product, get_products } from '@/api/api';
import AdminViewProductInfoComponent from './AdminViewProductInfoComponent.vue';

export default {
    name: 'AdminAllProductsComponent',
    components: {
    DashboardProductCardComponent,
    AdminViewProductInfoComponent
},
    data() {
        return {
            items: {},
            isDropdownOpen: false,
            selectedOption: null,
            isAnotherDropdownOpen: false,
            selectedAnotherOption: null,
            showModal: false,
            viewedItem: null

        };
    },
    methods: {
        close_modal: function () {
            this.showModal= false;
            this.viewedItem = null;
        },
        delete_product_handler: function (item_key) {
            delete_product(this.items[item_key].product_id, localStorage.getItem('session_id')).then((response) => {
                if (response.status === 0) {
                    delete this.items[item_key];
                }
            });
        },
        toggleDropdown: function () {
            this.isDropdownOpen = !this.isDropdownOpen;
        },
        selectOption: function (option) {
            this.selectedOption = option;
            this.isDropdownOpen = false; // Close the dropdown after selection
        },
        toggleAnotherDropdown: function () {
            this.isAnotherDropdownOpen = !this.isAnotherDropdownOpen;
        },
        selectAnotherOption: function (option) {
            this.selectedAnotherOption = option;
            this.isAnotherDropdownOpen = false;
        },
        view_product_handler: function (item_key) {
            this.viewedItem = this.items[item_key];
            this.showModal = true;
        }
    },
    computed: {
        buttonText: function () {
            return this.selectedOption || 'Dropdown';
        }
    },
    created() {
        get_products().then((response) => {
            for (const [item_key, item_info] of Object.entries(response)) {
                this.items[item_key] = item_info;
            }
        });
    }
}
</script>

<style scoped>
.component-window {
    background-color: white;
    flex: 1;
    margin: 0.5rem 0rem 0.5rem 0;
    float: left;
    width: 700px;
}

.search-bar {
    margin: 1rem 1rem 0rem 1rem;
    display: inline-block;
}

.search {
    width: 75%;
    border: 2px solid #3C5E55;
    padding: 5px;
    height: 20px;
    border-radius: 5px 5px 5px 5px;
    outline: none;
    color: #9DBFAF;
}

.search:focus {
    color: #3C5E55;
}

.search-btn {
    width: 40px;
    height: 40px;
    border: 1px solid white;
    background: white;
    text-align: center;
    color: #3C5E55;
    cursor: pointer;
    font-size: 20px;
}

.dropdown {
    display: inline-block;
    margin: 1rem 1rem 0rem 1rem;
}

.dropbtn {
    /* width: 95px; */
    background-color: #BBBBBB;
    color: black;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    font-weight: bold;
    font-family: Inter;
    font-size: 12px;
}

/* Dropdown content (hidden by default) */
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 95px;
    z-index: 1;
    font-family: Inter;
    font-size: 12px;
}

/* Show the dropdown content when the dropdown button is clicked */
.show {
    display: block;
}

/* Style the dropdown menu links */
.dropdown-content a {
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

/* Change color on hover */
.dropdown-content a:hover {
    background-color: #ddd;
}

.products {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}
</style>