import { createApp, ref } from 'vue'
import App from './App.vue'
import router from './router'

const app = createApp(App).use(router);

app.config.globalProperties.greencycle_loggedin = ref(0);
app.config.globalProperties.greencycle_user_id = ref(null);
app.config.globalProperties.greencycle_username = ref(null);
app.config.globalProperties.greencycle_description = ref(null);
app.config.globalProperties.greencycle_sessionid = ref(null);

app.mount('#app');

