<template>
    <div class="component-window">
        <SellerAddProductComponent
            v-if="showProductModal"
            :editing-state="editing_state"
            :item-edited="item_edited"
            @close-modal="close_modal"
            @success="success"
            @image-uploaded="updateProductImage"
        />
        <div class="search-bar">
            <input class="search" type="text" placeholder="Search...">
            <button type="submit" class="search-btn">
                <i class="fa fa-search"></i>
            </button>
        </div>
        <div class="dropdown">
            <button class="dropbtn" @click="toggleDropdown">{{ selectedOption || 'Posted' }} &#9660;</button>
            <div class="dropdown-content" :class="{ show: isDropdownOpen }">
                <a @click="selectOption('Posted')">Posted</a>
                <a @click="selectOption('Pending')">Pending</a>
            </div>
        </div>
        <div class="addproduct-btn-container">
            <button @click="editing_state = false; showProductModal = true" class="addproduct-btn"> Add product </button>
        </div>
        <div class="products">
            <DashboardProductCardComponent v-for="value, key in items" :key="key" :item_key="key" :item_info="value"
                :is_pending="value.is_pending" :is_admin="false" :product_image="productImages[key]"
                @edit-product="(item_key) => edit_product_handler(item_key)"
                @delete-product="(item_key) => delete_product_handler(item_key)" />
        </div>
    </div>
</template>

<script>
// Import the Font Awesome CSS
import 'font-awesome/css/font-awesome.css';
import SellerAddProductComponent from './SellerAddProductComponent.vue';
import DashboardProductCardComponent from '@/components/Dashboard/DashboardProductCardComponent.vue';
import { delete_product, delete_pending_product, get_user_products } from '@/api/api';

export default {
    name: 'SellerMyProductsDashboard',
    components: {
        DashboardProductCardComponent,
        SellerAddProductComponent
    },
    data() {
        return {
            editing_state: false,
            isDropdownOpen: false,
            items: {},
            item_edited: null,
            selectedOption: null,
            showProductModal: false,
            productImages: {}, // Store product images using product keys
        };
    },
    methods: {
        success: function () {
            this.showProductModal = false;
            get_user_products(localStorage.getItem('session_id')).then((response) => {
                if (response.status === 0) {
                    for (const [item_key, item_info] of Object.entries(response.items)) {
                        this.items[item_key] = item_info;
                    }
                }
            });
        },
        close_modal: function () {
            this.showProductModal = false;
            this.editing_state = false;
        },
        delete_product_handler: function (item_key) {
            if (this.items[item_key].is_pending) {
                delete_pending_product(this.items[item_key].product_id, localStorage.getItem('session_id')).then((response) => {
                    if (response.status === 0) {
                        delete this.items[item_key];
                    }
                });
            } else {
                delete_product(this.items[item_key].product_id, localStorage.getItem('session_id')).then((response) => {
                    if (response.status === 0) {
                        delete this.items[item_key];
                    }
                });
            }
        },
        edit_product_handler: function (item_key) {
            this.item_edited = this.items[item_key];
            this.editing_state = true;
            this.showProductModal = true
        },
        toggleDropdown: function () {
            this.isDropdownOpen = !this.isDropdownOpen;
        },
        selectOption: function (option) {
            this.selectedOption = option;
            this.isDropdownOpen = false; // Close the dropdown after selection
        }
    },
    // Method to update the product image when an image is uploaded in SellerAddProductComponent
    updateProductImage(key, imageData) {
      // Update the image data for the corresponding product in the productImages object
      this.$set(this.productImages, key, imageData);
    },
    computed: {
        buttonText: function () {
            return this.selectedOption || 'Dropdown';
        }
    },
    created() {
        get_user_products(localStorage.getItem('session_id')).then((response) => {
            if (response.status === 0) {
                for (const [item_key, item_info] of Object.entries(response.items)) {
                    this.items[item_key] = item_info;
                }
            }
        });
    }
}
</script>

<style scoped>

.addproduct-btn:hover{
  transform: scale(1.03);
  transition: transform 0.1s ease-in-out;
}
.component-window {
    background-color: white;
    flex: 1;
    margin: 0.5rem 0.5rem 0.5rem 0;
}

.search-bar {
    float: left;
    margin: 1rem 1rem 0rem 1rem;
}

.search {
    width: 75%;
    border: 2px solid #3C5E55;
    padding: 5px;
    height: 20px;
    border-radius: 5px 5px 5px 5px;
    outline: none;
    color: #9DBFAF;
}

.search:focus {
    color: #3C5E55;
}

.search-btn {
    width: 40px;
    height: 40px;
    border: 1px solid white;
    background: white;
    text-align: center;
    color: #3C5E55;
    cursor: pointer;
    font-size: 20px;
}

/* Dropdown button style */
.dropdown {
    display: inline-block;
    margin: 1rem 0rem 0rem 1rem;
}

.dropbtn {
    width: 95px;
    background-color: #BBBBBB;
    color: black;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    font-weight: bold;
    font-family: Inter;
    font-size: 12px;
}

/* Dropdown content (hidden by default) */
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 95px;
    z-index: 1;
    font-family: Inter;
    font-size: 12px;
}

/* Show the dropdown content when the dropdown button is clicked */
.show {
    display: block;
}

/* Style the dropdown menu links */
.dropdown-content a {
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

/* Change color on hover */
.dropdown-content a:hover {
    background-color: #ddd;
}

/* add product button style */

.addproduct-btn-container {
    display: inline-block;
    margin: 0rem 0rem 1rem 1rem;
}

.addproduct-btn {
    width: 95px;
    background-color: #4F7959;
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    font-weight: bold;
    font-family: Inter;
    font-size: 12px;
}

.seller-product-card {
    width: 140px;
    height: auto;
    margin: 1rem 1rem 1rem 1rem;
    background-color: #F1F1F1;
    display: flex;
    flex-direction: column;
    /* Stack the inner elements vertically */
    align-items: center;
    /* Center the inner elements horizontally */
}
.search-btn:hover{
  transform: scale(1.03);
  transition: transform 0.1s ease-in-out;
}
.product-img-frame {
    width: 80px;
