    vertical-align: top;
}

.edit-button {
    margin-top: 10px;
    text-decoration: none;
    color: #4F7959;
    /* Customize the edit button color */
    display: flex;
    align-items: center;
    font-size: 13px;
}

.icon-small {
    margin-right: 5px;
    width: 15px;
    height: 15px;
}
</style>