<template>
    <div class="newsfeed-card-container">
        <div class="photo">
            <div class="poster"></div>
        </div>
        <div class="detail">
            <div class="title">{{ post.title }}</div>
            <div class="author">{{ post.post_description }}</div>
            <div class="time"> <strong>Date Posted: </strong> {{ post.timestamp_column }}</div>
        </div>
        <div class="engagement">
            <div class="favorite">
                <button class="favorite-button" @click="toggleFavorite">
      <img v-if="!isFavorite" src="@/assets/star.png" alt="Unselected Favorite">
      <img v-else src="@/assets/shaded-star.png" alt="Selected Favorite">
    </button>
            </div>
            <div class="dummy"></div>
            <div class="likes" @click="toggleLikes">
                <button class="heart-button" @click="toggleHeart">
      <img v-if="!isFilled" src="@/assets/unshaded.png" alt="Unshaded Heart">
      <img v-else src="@/assets/shaded.png" alt="Shaded Heart">
    </button>
                <div>{{ likesCount }}</div>
            </div>
            <div class="views" @click="userAuthenticated && toggleViews">
                <img src="@/assets/eye.png" alt="">
                <div>{{ viewsCount }}</div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'NewsfeedCardComponent',
    props: ['post'],
    components: {
    },
    data() {
        return {
            newsfeed: {},
            likesCount: 0,
            viewsCount: 0,
            userAuthenticated: false,
            isFilled: false,
            isFavorite: false,
        };
    },
    methods: {
        toggleLikes(){
            this.likesCount = this.likesCount === 0 ? 1 : 0;
        },
        toggleViews() {
      this.viewsCount = this.viewsCount === 0 ? 1 : 0;
    },
    toggleHeart() {
      this.isFilled = !this.isFilled;
    },
     toggleFavorite() {
      this.isFavorite = !this.isFavorite;
    }
    },
}
</script>

<style scoped>
.author {
    font-size: 0.85rem;
}

.time{
    font-size: 0.85rem;
}

.detail {
    padding: 1rem;
}

.dummy {
    flex: 1;
    border-top: solid lightgrey 0.125rem;
    border-bottom: solid lightgrey 0.125rem;
}

.engagement {
    display: flex;
}

.favorite {
    display: flex;
    align-items: center;
    border: solid lightgrey 0.125rem;
    border-radius: 0 0 0 1rem;
    padding: 0.5rem 1rem 0.5rem 1rem;
}

.favorite-button {
  background: none;
  border: none;
  cursor: pointer;
  display: inline-block;
}

/* Optionally, add styles to the images for sizing */
img {
  width: 24px; /* Adjust the width as needed */
  height: 24px; /* Adjust the height as needed */
}

.heart-button {
  background: none;
  border: none;
  cursor: pointer;
  display: inline-block;
}

/* Optionally, add styles to the images for sizing */
img {
  width: 24px; /* Adjust the width as needed */
  height: 24px; /* Adjust the height as needed */
}

.likes {
    display: flex;
    align-items: center;
    border-top: solid lightgrey 0.125rem;
    border-bottom: solid lightgrey 0.125rem;
    padding: 0.5rem;
}

.newsfeed-card-container {
    background-color: white;
    /* border: solid 1rem; */
    /* border-color: blue; */
    border-radius: 1rem;
}

.poster {
    background: url('@/assets/batteries.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 10rem;
    /* width: 5rem; */
    border-radius: 1rem 1rem 0 0;
}

.title {
    font-weight: 700;
    min-height: 2rem;
}

.views {
    display: flex;
    align-items: center;
    border-top: solid lightgrey 0.125rem;
    border-bottom: solid lightgrey 0.125rem;
    border-right: solid lightgrey 0.125rem;
    border-radius: 0 0 1rem 0;
    padding: 0.5rem;
}
</style>