<template>
    <MarketplaceHeaderComponent>Dashboard</MarketplaceHeaderComponent>
    <div class="buyer-dashboard-container">
        <div class="banner">
        </div>
        <div class="profile-picture-container">
            <div class="profile-picture">

            </div>
        </div>
        <div class="profile">
            <div class="edit-profile">
                <ProfileEditButton text="Edit Profile" toggled-text="Save" @toggle-changed="(state) => edit_profile_toggle(state)"/>
            </div>
            <div class="profile-info">
                <div class="display-name">
                    {{ user_profile.name }}
                </div>
                <div class="username">
                    {{ user_profile.username }}
                </div>
                <div class="location">
                    {{ user_profile.location }}
                </div>
            </div>
            <div class="profile-description">
                "{{ user_profile.bio }}"
            </div>
            <div v-if="!editing_profile">
                <div class="menu-selector">
                    <div :ref="(el) => menu_item[0] = el" @click="menu_toggle(0)" class="menu-selector-item">
                        To Ship
                    </div>
                    <div :ref="(el) => menu_item[1] = el" @click="menu_toggle(1)"  class="menu-selector-item">
                        To Receive
                    </div>
                    <div :ref="(el) => menu_item[2] = el" @click="menu_toggle(2)"  class="menu-selector-item">
                        Completed
                    </div>
                </div>
                <div class="menu">
                    <OrderItemCardComponent v-for="value, key in order_items" :key="key" :item_info="value"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ProfileEditButton from '@/components/Dashboard/ProfileEditButton.vue';
import OrderItemCardComponent from '@/components/OrderItemCardComponent.vue'
import MarketplaceHeaderComponent from '@/components/common/MarketplaceHeaderComponent.vue';
import { get_user_orders, get_user_profile } from '@/api/api';

export default {
    name: 'BuyerDashboard',
    data() {
        return {
            current_menu: 0,
            menu_item: [],
            items: [],
            order_status: 1,
            user_profile: {
                name: null,
                username: null,
                location: null,
                bio: null
            },
            editing_profile: false
        }
    },
    components: {
        ProfileEditButton,
        OrderItemCardComponent,
        MarketplaceHeaderComponent
    },
    methods: {
        menu_toggle: function (menu) {
            if (this.current_menu !== menu) {
                this.menu_item[this.current_menu].style.outline = 'none';
                this.menu_item[menu].style.outline = 'solid';
                this.current_menu = menu;
                this.order_status = menu + 1;
            }
        },
        edit_profile_toggle: function (state) {
            this.editing_profile = state;
        }
    },
    computed: {
        order_items: function () {
            return this.items.filter((i) => { return i.order_status === this.order_status });
        }
    },
    created() {
        get_user_orders(localStorage.getItem('session_id')).then((response) => {
            if (response.status === 0) {
                for (const [key, value] of Object.entries(response.items)) {
                    this.items[key] = value;
                }
            }
        });
        get_user_profile(localStorage.getItem('session_id')).then((response) => {
            if (response.status === 0) {
                this.user_profile.name = response.user_name;
                this.user_profile.username = response.user_username;
                this.user_profile.location = response.user_location;
                this.user_profile.bio = response.user_bio;
            }
        });
    },
    mounted() {
        this.menu_item[this.current_menu].style.outline = 'solid';
    }
}
</script>

<style scoped>
/* * {
    outline: 1px solid;
} */

.banner {
    background: url('@/assets/tree-bg.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 20rem;
    opacity: 0.5;
    z-index: -1;
}

.display-name {
    color: white;
    font-size: 2.5rem;
    font-weight: 800;
    text-shadow:
        -2px 2px 1px #3C5E55,
        2px 2px 1px #3C5E55,
        2px -2px 1px #3C5E55,
        -2px -2px 1px #3C5E55,
        -2px 0px 1px #3C5E55,
        2px 0px 1px #3C5E55,
        0px 2px 1px #3C5E55,
        0px 2px 1px #3C5E55;
}

.edit-profile {
    text-align: end;
    padding: 1rem 1rem 0 0;
}

.location {
    font-size: 1.5rem;
    font-weight: 700;
}

.menu {
    padding-inline: 5rem;
}

.menu-selector {
    background-color: white;
    display: flex;
    margin: 1rem 5rem;
}

.menu-selector-item {
    color: #4F7959;
    flex: 1;
    font-size: 2rem;
    font-weight: 700;
    outline-color: #4F7959;
    padding: 1rem 2rem;
    text-align: center;
}

.profile-description {
    font-weight: 500;
    text-align: center;
}

.profile {
    background-color: #F1F1F1;
    /* min-height: 30rem; */
    margin-top: -4rem;
    padding-bottom: 1rem;
    /* z-index: -1; */
}

.profile-info {
    text-align: center;
    height: 7.5rem;
}

.profile-picture {
    background: url('@/assets/catriona.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 7.5rem;
    height: 15rem;
    width: 15rem;
}

.profile-picture-container {
    margin-inline: auto;
    margin-top: -11rem;
    z-index: 1;
}

.username {
    color: white;
    font-size: 1.5rem;
    font-weight: 800;
    text-shadow:
        -2px 2px 1px #3C5E55,
        2px 2px 1px #3C5E55,
        2px -2px 1px #3C5E55,
        -2px -2px 1px #3C5E55,
        -2px 0px 1px #3C5E55,
        2px 0px 1px #3C5E55,
        0px 2px 1px #3C5E55,
        0px 2px 1px #3C5E55;
}

.buyer-dashboard-container {
    display: flex;
    flex-direction: column;
    font-family: 'Inter';
}

.edit-profile:hover{
  transform: scale(1.03);
  transition: transform 0.1s ease-in-out;
}

</style>