<template>
    <button :ref="(el) => button_el = el" class="profile-edit-button" @click="toggle_handler">
        <div class="icon">

        </div>
        <div :ref="(el) => button_text_el = el" class="text">
            <template v-if="toggled">
                {{ toggledText }}
            </template>
            <template v-else>
                {{ text }}
            </template>
        </div>
    </button>
</template>

<script>
export default {
    name: 'ProfileEditButton',
    props: ['text', 'toggledText'],
    data() {
        return {
            button_el: null,
            button_text_el: null,
            toggled: false
        }
    },
    components: {
    },
    methods: {
        toggle_handler: function () {
            this.toggled = !this.toggled;
            if (this.toggled) {
                this.button_el.style.backgroundColor = '#4F7959';
                this.button_text_el.style.color = 'white';
            } else {
                this.button_el.style.backgroundColor = '#D9D9D9';
                this.button_text_el.style.color = 'black';
            }
            this.$emit('toggleChanged', this.toggled);
        }
    }
}
</script>

<style>
.icon {
    background: url('@/assets/edit-icon.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 2rem;
    width: 2rem;
}

.profile-edit-button {
    background-color: #D9D9D9;
    display: inline-flex;
    cursor: pointer;
    align-items: center;
    padding: 0.5rem;
}

.text {
    font-family: 'Inter';
    font-size: 1.25rem;
    font-weight: 600;
    margin-left: 0.5rem;
}
</style>