<template>
    <MarketplaceHeaderComponent />
    <MarketplaceSearchComponent />
    <div class="shopping-cart-page-container">
        <div class="cart-page-header-container">
            Shopping Cart
        </div>
        <div class="generic-container-0">
            <div class="generic-container-1">
                <div class="all-items-checkbox-container">
                    <input ref="select_all" @click="select_all_products" type="checkbox" id="idk" name="check" value="box">
                    <label for="idk">All Items</label>
                </div>
                <div class="cart-items-container">
                    <CartItemCardComponent v-for="value, key in items" :key="key" :item_key="key" :item_info="value"
                        :select_all="select_all" :select_state_guard="select_state_guard" @add-quantity="(item_key) => add_quantity(item_key)"
                        @item-deselected="(item_key) => item_deselected(item_key)"
                        @item-selected="(item_key) => item_selected(item_key)"
                        @remove-item="(item_key) => remove_item(item_key)"
                        @subtract-quantity="(item_key) => subtract_quantity(item_key)" />
                </div>
            </div>
            <div class="generic-container-2">
                <div class="order-summary-container">
                    <div class="order-summary-header">
                        Order Summary
                    </div>
                    <div class="order-subtotal-container">
                        <div class="generic-container-4">
                            Subtotal
                        </div>
                        <div class="order-subtotal-amount">
                            {{ order_total }}
                        </div>
                    </div>
                    <div class="checkout-button">
                        <input @click="checkout" type="button" value="Checkout now">
                    </div>
                </div>
            </div>
        </div>
    </div>
    <FooterComponent />
</template>

<script>
import FooterComponent from '@/components/common/FooterComponent.vue';
import MarketplaceHeaderComponent from '@/components/common/MarketplaceHeaderComponent.vue';
import MarketplaceSearchComponent from '@/components/common/MarketplaceSearchComponent.vue';
import CartItemCardComponent from '@/components/Product/CartItemCardComponent.vue';
// import { ref } from 'vue';
import { get_cart, get_product_with_id, pre_checkout, remove_from_cart, update_cart } from '@/api/api';

export default {
    name: 'ShoppingCartPage',
    components: {
        FooterComponent,
        MarketplaceHeaderComponent,
        MarketplaceSearchComponent,
        CartItemCardComponent
    },
    data() {
        return {
            items: {},
            selected_items: {},
            select_all: false,
            select_state_guard: false
        }
    },
    methods: {
        add_quantity: function (item_key) {
            update_cart(this.items[item_key].product_id, this.items[item_key].quantity + 1, localStorage.getItem('session_id')).then((response) => {
                if (response.status === 0) {
                    this.items[item_key].quantity++;
                }
            });
        },
        item_deselected: function (item_key) {
            this.$refs.select_all.checked = false;
            this.select_state_guard = true;
            this.select_all = false;
            delete this.selected_items[item_key];
        },
        item_selected: function (item_key) {
            this.selected_items[item_key] = this.items[item_key];
        },
        remove_item: function (item_key) {
            remove_from_cart(this.items[item_key].product_id, localStorage.getItem('session_id')).then((response) => {
                if (response.status === 0) {
                    delete this.items[item_key];
                    delete this.selected_items[item_key];
                }
            });
        },
        select_all_products: function (e) {
            this.select_state_guard = false;
            this.select_all = e.target.checked;
        },
        subtract_quantity: function (item_key) {
            if (this.items[item_key].quantity === 1) {
                return;
            }
            update_cart(this.items[item_key].product_id, this.items[item_key].quantity - 1, localStorage.getItem('session_id')).then((response) => {
                if (response.status === 0) {
                    this.items[item_key].quantity--;
                }
            });
        },
        checkout: function () {
            pre_checkout(this.selected_items).then((response) => {
                if (response.status === 0) {
                    this.$router.push({ name: 'CheckoutPage', params: { id: response.id } });
                }
            });
        }

    },
    computed: {
        order_total: function () {
            let total = 0;
            for (const item of Object.entries(this.selected_items)) {
                total += item[1].product_price * item[1].quantity;
            }
            return (total / 100).toFixed(2);
        }
    },
    created() {
        get_cart(localStorage.getItem('session_id')).then((response) => {
            if (response.status === 0) {
                for (const [item_key, cart_item] of Object.entries(response.items)) {
                    get_product_with_id(cart_item.product_id).then((response) => {
                        response.quantity = cart_item.quantity;
                        this.items[item_key] = response;
                    });
                }
            }
        });
    }
}
</script>

