<template>
    <MarketplaceHeaderComponent>Administrator</MarketplaceHeaderComponent>
    <div class="admin-dashboard-container">
        <div class="header">
            Admin Dashboard
        </div>
        <div class="menu">
            <div class="menu-selector">
                <div :ref="(el) => menu_item[0] = el" @click="menu_toggle(0)" class="menu-selector-item">
                    My Profile
                </div>
                <div :ref="(el) => menu_item[1] = el" @click="menu_toggle(1)" class="menu-selector-item">
                    All Products
                </div>
                <div :ref="(el) => menu_item[2] = el" @click="menu_toggle(2)" class="menu-selector-item">
                    Pending Products
                </div>
                <div :ref="(el) => menu_item[3] = el" @click="menu_toggle(3)" class="menu-selector-item">
                    Users
                </div>
                <div :ref="(el) => menu_item[4] = el" @click="menu_toggle(4)" class="menu-selector-item">
                    Audit Trail
                </div>
                <div :ref="(el) => menu_item[5] = el" @click="menu_toggle(5)" class="menu-selector-item">
                    Reports
                </div>
            </div>
            <div class="menu-window">
                <AdminProfileComponent v-if="current_menu === 0" />
                <AdminAllProductsComponent v-if="current_menu === 1" />
                <AdminPendingProductsComponent v-if="current_menu === 2" />
                <AdminUsersComponent v-if="current_menu === 3" />
                <AdminAuditTrailComponent v-if="current_menu === 4" />
                <AdminReportGenerationComponent v-if="current_menu === 5" />
            </div>
        </div>
    </div>
</template>

<script>
import MarketplaceHeaderComponent from '@/components/common/MarketplaceHeaderComponent.vue';
import AdminAllProductsComponent from '@/components/Dashboard/AdminDashboard/AdminAllProductsComponent.vue';
import AdminAuditTrailComponent from '@/components/Dashboard/AdminDashboard/AdminAuditTrailComponent.vue';
import AdminPendingProductsComponent from '@/components/Dashboard/AdminDashboard/AdminPendingProductsComponent.vue';
import AdminUsersComponent from '@/components/Dashboard/AdminDashboard/AdminUsersComponent.vue';
import AdminProfileComponent from '@/components/Dashboard/AdminDashboard/AdminProfileComponent.vue';
import AdminReportGenerationComponent from '@/components/Dashboard/AdminDashboard/AdminReportGenerationComponent.vue';

export default {
    name: 'AdminDashboard',
    data() {
        return {
            current_menu: 0,
            menu_item: [],
        }
    },
    components: {
        MarketplaceHeaderComponent,
        AdminAllProductsComponent,
        AdminAuditTrailComponent,
        AdminPendingProductsComponent,
        AdminUsersComponent,
        AdminProfileComponent,
        AdminReportGenerationComponent,
    },
    methods: {
        menu_toggle: function (menu) {
            if (this.current_menu !== menu) {
                this.menu_item[this.current_menu].style.outline = 'none';
                this.menu_item[menu].style.outline = 'solid';
                this.current_menu = menu;
            }
        }
    },
    mounted() {
        this.menu_item[this.current_menu].style.outline = 'solid';
    }
}
</script>

<style scoped>
.header {
    color: #4F7959;
    font-size: 2rem;
    font-weight: 600;
    margin: 1rem 0 0 5rem;
    text-decoration: underline;
}

.menu {
    display: flex;
    margin: 1rem 5rem 5rem 5rem;
}

.menu-selector-item {
    background-color: white;
    color: #4F7959;
    cursor: pointer;
    font-size: 15px;
    font-weight: 600;
    margin: 0.5rem;
    outline-color: #4F7959;
    padding: 1rem 2rem;
    text-align: center;
}

.admin-dashboard-container {
    display: flex;
    flex-direction: column;
    font-family: 'Inter';
    background-color: #F1F1F1;
}

.menu-item {
    color: #4F7959;
    font-size: 2rem;
    font-weight: 600;
    padding: 1rem 2rem;
    text-align: center;
    background-color: white;
}
</style>