<template>
    <div class="component-window">
        <div class="bname-edit">
            Name
            <br><input placeholder="Mimma Sassa">
        </div>
        <div class="busername-edit">
            Username
            <br><input placeholder="@sassagurl69">
        </div>
        <div class="bemail-edit">
            Email
            <br><input placeholder="confirm new email">
        </div>
        <div class="bnewpassword-edit">
            New password
            <br><input placeholder="new password">
        </div>
        <div class="bcnewpassword-edit">
            Confirm new password
            <br><input placeholder="confirm new password">
        </div>
        <div class="edit-button">
            <ProfileEditButton text="Edit Profile" toggled-text="Save"
                @toggle-changed="(toggle_state) => edit_profile_toggle(toggle_state)" />
        </div>
    </div>
</template>

<script>
import { get_user_profile, update_user } from '@/api/api';
import ProfileEditButton from '@/components/Dashboard/ProfileEditButton.vue';

export default {
    name: 'AdminEditProfileComponent',
    data() {
        return {
            user_info: {
                name: null,
                location: null,
                bio: null
            }
        }
    },
    components: {
        ProfileEditButton,
    },
    methods: {
        edit_profile_toggle: function (toggle_state) {
            if (!toggle_state) {
                update_user().then((response) => {
                    if (response.status === 0) {
                        get_user_profile(localStorage.getItem('session_id')).then((response) => {
                            if (response.status === 0) {
                                this.user_info.name = response.name;
                                this.user_info.location = response.location;
                                this.user_info.bio = response.bio;
                            }
                        });
                    }
                });
            }
        }
    },
    mounted() {
        get_user_profile(localStorage.getItem('session_id')).then((response) => {
            if (response.status === 0) {
                this.user_info.name = response.name;
                this.user_info.location = response.location;
                this.user_info.bio = response.bio;
            }
        });
    }
}
</script>

<style scoped>
.component-window {
    position: relative;
    /* Updated position */
    background-color: white;
    margin: 0.5rem 0.5rem 0.5rem 0;
    padding-top: 40px;
    /* Add padding to ensure content doesn't go under the button */
}

.edit-button {
    position: absolute;
    /* Absolute positioning within the component-window */
    top: 10px;
    /* Adjust the value as needed */
    right: 10px;
    /* Adjust the value as needed */
}

.bname-edit,
.busername-edit,
.bemail-edit,
.bnewpassword-edit,
.bcnewpassword-edit {
    display: inline-block;
    font-weight: 600;
    margin: 10px 20px 10px 10px;
}
</style>