<template>
    <div class="api-test">
        <div class="api">
            <ProfileEditButton @click="api_wrapper(get_session_user)" class="btn">get_session_user</ProfileEditButton>
            <ProfileEditButton @click="api_wrapper(get_user_products)" class="btn">get_user_products</ProfileEditButton>
            <ProfileEditButton @click="api_wrapper(get_sesion_user)" class="btn"></ProfileEditButton>
            <ProfileEditButton @click="api_wrapper(get_sesion_user)" class="btn"></ProfileEditButton>
            <ProfileEditButton @click="api_wrapper(get_sesion_user)" class="btn"></ProfileEditButton>
            <ProfileEditButton @click="api_wrapper(get_sesion_user)" class="btn"></ProfileEditButton>
            <ProfileEditButton @click="api_wrapper(get_sesion_user)" class="btn"></ProfileEditButton>
            <ProfileEditButton @click="api_wrapper(test)" class="btn">test</ProfileEditButton>
        </div>
        <div class="output">
            <div class="output-header">Output</div>
            <div ref="output_text" class="output-text"></div>
        </div>
    </div>
</template>

<script>
import ProfileEditButton from '@/components/Dashboard/ProfileEditButton.vue';
import {get_session_user, get_user_products, test} from '@/api/api';
const AsyncFunction = (async () => {}).constructor;

export default {
    name: 'ApiTest',
    components: {
        ProfileEditButton
    },
    data() {
        return {
            get_session_user: get_session_user,
            get_user_products: get_user_products,
            test: test,
            session_id: localStorage.getItem('session_id')
        }
    },
    methods: {
        api_wrapper: async function (func) {
            if (func instanceof AsyncFunction) {
                this.$refs.output_text.textContent = JSON.stringify(await func());
            } else {
                this.$refs.output_text.textContent = func();
            }
        }
    }
}
</script>

<style scoped>
.api {
    background-color: dimgrey;
    width: 50%;
}

.api-test {
    display: flex;
    font-family: 'Inter';
    min-height: 50rem;
}

.btn {
    margin: 0.5rem;
}

.output {
    background-color: grey;
    width: 50%;
}

.output-header {
    font-size: 1.5rem;
    font-weight: 600;
    margin: 0.5rem;
}

.output-text {
    font-family: 'Consolas';
    font-size: 1.25rem;
    margin: 0.5rem;
}
</style>