<template>
    <div class="component-window">
        <div v-if="!is_editing" class="profile-section">
            <div class="profile-pic-frame">
                <img src="@/assets/bts.png" alt="Profile Picture" class="profile-image">
            </div>
            <div class="profile-info">
                <h1 class="name">{{ user_info.name }}</h1>
                <p class="username">@{{ user_info.username }}</p>
                <p class="location"> {{ user_info.location }}</p>
            </div>
        </div>
        <div v-if="is_editing" class="edit-profile-section">
            <div class="bname-edit">
                Name
                <br><input v-model="user_info.name">
            </div>
            <div class="busername-edit">
                Username
                <br><input v-model="user_info.username">
            </div>
            <div class="bemail-edit">
                Email
                <br><input placeholder="Confirm new email">
            </div>
            <div class="bnewpassword-edit">
                New password
                <br><input placeholder="New password">
            </div>
            <div class="bcnewpassword-edit">
                Confirm new password
                <br><input placeholder="Confirm new password">
            </div>
        </div>
        <div class="edit-button">
            <ProfileEditButton text="Edit Profile" toggled-text="Save"
                @toggle-changed="(toggle_state) => edit_profile_toggle(toggle_state)" />
        </div>
    </div>
</template>

<script>
import { get_user_profile, update_user } from '@/api/api';
import ProfileEditButton from '@/components/Dashboard/ProfileEditButton.vue';

export default {
    name: 'AdminProfileComponent',
    data() {
        return {
            is_editing: false,
            user_info: {
                username: null,
                name: null,
                location: null,
                bio: null
            }
        };
    },
    components: {
        ProfileEditButton,
    },
    methods: {
        edit_profile_toggle: function (toggle_state) {
            if (!toggle_state) {
                update_user(
                    this.user_info.name,
                    this.user_info.location,
                    this.user_info.bio,
                    localStorage.getItem('session_id')
                ).then((response) => {
                    if (response.status === 0) {
                        get_user_profile(localStorage.getItem('session_id')).then((response) => {
                            if (response.status === 0) {
                                this.user_info.name = response.user_name;
                                this.user_info.location = response.user_location;
                                this.user_info.bio = response.user_bio;
                                this.editing = toggle_state;
                            }
                        });
                    }
                });
            }
            this.is_editing = toggle_state;
        }
    },
    mounted() {
        get_user_profile(localStorage.getItem('session_id')).then((response) => {
            if (response.status === 0) {
                this.user_info.username = response.user_username;
                this.user_info.name = response.user_name;
                this.user_info.location = response.user_location;
                this.user_info.bio = response.user_bio;
            }
        });
    }
}
</script>

<style scoped>
.component-window {
    background-color: white;
    flex: 1;
    margin: 0.5rem 0rem 0.5rem 0;
    float: left;
    width: 700px;
}

.profile-section {
    display: flex;
    align-items: center;
    /* this will align the children vertically in the center */
    justify-content: space-between;
}

.edit-button {
    align-self: flex-start;
    margin: 1rem 1rem 1rem 1rem;
}

.profile-pic-frame {
    align-self: center;
    border: 1px solid #e1e1e1;
    font-family: Arial, sans-serif;
    background-color: #fff;
    width: 200px;
    height: 200px;
    margin: 1rem 1rem 1rem 1rem;
    display: inline-block;
}

.profile-image {
    width: 200px;
}

.profile-info {
    align-self: flex-start;
    display: inline-block;
    flex-direction: column;
    margin: 1rem 1rem 1rem 0rem;
}

.name {
    font-size: 25px;
    line-height: 1.5;
    margin: 0;
}

.username {
    color: #888;
    font-size: 14px;
    line-height: 1.5;
    margin: 0;
}

.location {
    font-size: 14px;
    margin: 10px 0;
    color: #444;
    line-height: 1.5;
    margin: 0;
}
</style>