<template>
    <div class="slideshow-component">
        <button class="slideshow-button" @click="slide_prev">
            &lt;
        </button>
        <div class="slideshow-container">
            <div class="slide-container" :ref="(el) => slides_container = el">
                <div v-for="slide, key in slides" :key="key" class="slide">
                    <div class="slideshow-title">
                        {{ slide.title }}
                    </div>
                    <div class="slideshow-content">
                        {{ slide.content }}
                    </div>
                </div>
                <div v-if="(slides.length % 2) !== 0" class="slide">
                </div>
            </div>
            <div class="dots-container">
                <div v-for="slide in slides.length - 2" :key="slide" :ref="(el) => dots[slide - 1] = el"
                    @click="goto_slide(slide - 1)" class="dot"></div>
            </div>
        </div>
        <button class="slideshow-button" @click="slide_next">
            &gt;
        </button>
    </div>
</template>

<script>

export default {
    name: 'SlideshowComponent',
    data() {
        return {
            current_slide: 0,
            dots: [],
            slides: [
                { title: 'Effects of Chemical Waste! Fake', content: 'Chemical waste, such as old computers, batteries, old applicances, and electronic gadgets poses significant risks, including water and soil contamination, health hazards, and long-term environmental damage. Strict handling, storage, and disposal protocols are vital to prevent these adverse effects.' },
                { title: 'Effects of Industrial Waste!', content: 'Industrial waste, such as scrap metal, construction debris, and plastic waste from packaging can lead to environmental pollution, including water and air pollution. It may also have economic consequences due to resource depletion and cleanup costs. Ensuring responsible disposal and management is crucial.' },
                { title: 'Effects of Commercial Waste!', content: 'Commercial waste, such as paper waste, used printer cartridges, old computers, food packaging, cardboard boxes, plastic bags, and unsold merchandise, if not managed properly, can result in overflowing landfills, resource wastage, and increased greenhouse gas emissions. Businesses can reduce these effects by implementing recycling and waste reduction strategies.' },
                { title: 'Effects of Domestic Waste!', content: 'Industrial waste, such as scrap metal, construction debris, and plastic waste from packaging can lead to environmental pollution, including water and air pollution. It may also have economic consequences due to resource depletion and cleanup costs. Ensuring responsible disposal and management is crucial.' },
                { title: 'Effects of Agricultural Waste!', content: 'Agricultural waste, such as empty pesticide or herbicide containers, and pesticide runoff and excessive fertilizer use, can harm ecosystems, contaminate water sources, and affect biodiversity. Sustainable farming practices are essential for mitigating these effects.' },
                { title: 'Effects of Chemical Waste!', content: 'Chemical waste, such as old computers, batteries, old applicances, and electronic gadgets poses significant risks, including water and soil contamination, health hazards, and long-term environmental damage. Strict handling, storage, and disposal protocols are vital to prevent these adverse effects.' },
                { title: 'Effects of Industrial Waste! Fake', content: 'Industrial waste, such as scrap metal, construction debris, and plastic waste from packaging can lead to environmental pollution, including water and air pollution. It may also have economic consequences due to resource depletion and cleanup costs. Ensuring responsible disposal and management is crucial.' }
            ],
            slides_container: null,
            transition_ready: true,
        }
    },
    components: {
    },
    computed: {
        translate_amount: function () {
            return ((this.current_slide * (100 / (this.slides.length + (this.slides.length % 2)))) + (100 / (this.slides.length + (this.slides.length % 2))));
        }
    }
    ,
    methods: {
        goto_slide: async function (slide) {
            if (!this.transition_ready) {
                return;
            }
            this.transition_ready = false;
            const prev = this.current_slide;
            this.current_slide = slide;
            this.slides_container.style.transform = 'translateX(' + this.translate_amount * -1 + '%)';
            await new Promise(r => setTimeout(r, 1000));
            this.dots[prev].classList.remove('selected');
            this.dots[slide].classList.add('selected');
            this.transition_ready = true;
        },
        slide_prev: async function () {
            if (!this.transition_ready) {
                return;
            }
            this.transition_ready = false;
            const prev = this.current_slide;
            if (this.current_slide > 0) {
                this.current_slide -= 1;
                this.slides_container.style.transform = 'translateX(' + this.translate_amount * -1 + '%)';
                await new Promise(r => setTimeout(r, 1000));
                this.transition_ready = true;
            } else {
                this.current_slide -= 1;
                this.slides_container.style.transform = 'translateX(' + this.translate_amount * -1 + '%)';
                await new Promise(r => setTimeout(r, 250));
                this.slides_container.style.transition = 'all 0s';
                await new Promise(r => setTimeout(r, 750));
                this.current_slide = this.slides.length-3;
                this.slides_container.style.transform = 'translateX(' + this.translate_amount * -1 + '%)';
                await new Promise(r => setTimeout(r, 10));
                this.slides_container.style.transition = 'all 1s';
                this.transition_ready = true;
            }
            this.dots[prev].classList.remove('selected');
            this.dots[this.current_slide].classList.add('selected');
        },
        slide_next: async function () {
            if (!this.transition_ready) {
                return;
            }
            this.transition_ready = false;
            const prev = this.current_slide;
            if (this.current_slide < 4) {
                this.current_slide += 1;
                this.slides_container.style.transform = 'translateX(' + this.translate_amount * -1 + '%)';
                await new Promise(r => setTimeout(r, 1000));
                this.transition_ready = true;
            } else {
                this.current_slide += 1;
                this.slides_container.style.transform = 'translateX(' + this.translate_amount * -1 + '%)';
                await new Promise(r => setTimeout(r, 250));
                this.slides_container.style.transition = 'all 0s';
                await new Promise(r => setTimeout(r, 750));
                this.current_slide = 0;
                this.slides_container.style.transform = 'translateX(' + this.translate_amount * -1 + '%)';
                await new Promise(r => setTimeout(r, 10));
                this.slides_container.style.transition = 'all 1s';
                this.transition_ready = true;
            }
            this.dots[prev].classList.remove('selected');
            this.dots[this.current_slide].classList.add('selected');
        }
    },
    mounted() {
        this.slides_container.style.width = (100 * (this.slides.length + (this.slides.length % 2))) + '%';
        this.slides_container.style.transform = 'translateX(' + this.translate_amount * -1 + '%)';
        this.dots[this.current_slide].classList.add('selected');
    }
}
</script>

<style scoped>
.dot {
    background-color: #FFEDD9;
    cursor: pointer;
    height: 1rem;
    width: 1rem;
    border-radius: 1rem;
    margin-inline: 0.25rem;
}

.dots-container {
    display: flex;
    justify-content: center;
}

.selected {
    background-color: black;
}

.slide {
    width: 100%;
}

.slideshow-button {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    font-weight: 300;
    padding: 2rem;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.slideshow-button:hover {
    background-color: rgba(0, 0, 0, 0.1);
}

.slideshow-component {
    display: flex;
    background-color: #E1CAB2;
    font-family: 'Inter';
    padding: 1rem;
}

.slideshow-content {
    font-style: italic;
    font-weight: 500;
    margin-block: 0.5rem 0rem 0.5rem 0.5rem;
    padding: 0rem 0rem 0.5rem 0.5rem;
}

.slideshow-container {
    flex: 1;
    overflow: hidden;
}

.slide-container {
    display: flex;
    transition: 1s;
    /* transform: translateX(-12.5%); */
    /* width: 800%; */
}

.slideshow-title {
    font-size: 2rem;
    font-weight: 800;
    padding: 0rem 0rem 0.5rem 0.5rem;
}
</style>