<template>
    <div class="dashboard-product-card" ref="card">
        <div class="product-img-frame">
            <!-- Use the product_image prop to display the image -->
            <img :src="product_image" alt="Product image">
        </div>
        <div class="product-info">
            <h3 class="product-name">{{ item_info.product_name }}</h3>
            <span class="product-price">{{ (item_info.product_price / 100).toFixed(2) }}</span>
            <!-- <span class="product-price">{{ item_info.product_price }}</span> -->
            <p style="margin-block: 0.25rem;">Number of stocks: <span class="product-stock">{{ item_info.product_stock }}</span></p>
            <div class="buttons">
                <div v-if="!is_admin" @click="$emit('editProduct', item_key)" class="button">
                    <img src="@/assets/edit-icon.png" alt="Edit" class="icon-small">
                    <div class="button-text">
                        Edit
                    </div>
                </div>
                <div v-if="is_admin" @click="$emit('viewProduct', item_key)" class="button">
                    <img src="@/assets/info.png" alt="Edit" class="icon-small">
                    <div class="button-text">
                        View
                    </div>
                </div>
                <div v-if="!is_admin || !is_pending" @click="$emit('deleteProduct', item_key)" class="button">
                    <img src="@/assets/trash.png" alt="Delete"  class="icon-small">
                    <div class="button-text">
                        Delete
                    </div>
                </div>
                <!-- <div v-if="is_pending && is_admin" @click="$emit('approveProduct', item_key)" class="button">
                    <img src="@/assets/edit-icon.png" alt="Edit" class="icon-small">
                    <div class="button-text">
                        Approve
                    </div>
                </div> -->
                <div v-if="is_pending && !is_admin"></div>
                <p v-if="is_pending" style="justify-self: flex-end;">
                    Pending
                </p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'DashboardProductCardComponent',
    props: ['item_info', 'item_key', 'is_pending', 'is_admin', 'product_image'],
    components: {
    },
    mounted() {
        if (this.is_pending) {
            this.$refs.card.style.backgroundColor = '#f1f1c1';
        }
    }
}
</script>

<style scoped>
.buttons {
    display: grid;
    gap: 0.5rem;
    grid-template-columns: 1fr 1fr;
    justify-items: start;
    
}

.button {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.button-text {
    font-size: 0.75rem;
}

.dashboard-product-card {
    width: 140px;
    height: auto;
    margin: 1rem 1rem 1rem 1rem;
    background-color: #f1f1f1;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.product-img-frame {
    width: 80px;
    height: 80px;
    border: 2px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 1rem 1rem 1rem;
}

.dashboard-product-card img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
}

.product-info {
    display: flex;
    flex: 1;
    flex-direction: column;
    padding: 10px;
}

.product-name {
    flex: 1;
    font-size: 15px;
    font-family: Inter;
    margin: 0;
    line-height: 1.2;
    vertical-align: top;
}

.product-price {
    font-size: 20px;
    font-weight: bold;
    margin: 0;
    line-height: 1.2;
    vertical-align: top;
}

.product-info p {
    font-size: 10px;
    font-weight: 300;
    margin: 0;
    line-height: 1.2;
    vertical-align: top;
}

.icon-small {
    margin-right: 0.1rem;
    width: 15px;
    height: 15px;
}
</style>