        <div class="generic-container-1">
            <div class="order-details-container">
                <div class="underlined-header">
                    Order Details
                </div>
                <div class="order-items-container">
                    <div v-for="item, key in products" :key="key" class="order-item">
                        <div class="order-item-image">
                        </div>
                        <div class="order-item-details">
                            <div>
                                {{ item.product_name }}
                            </div>
                            <div class="order-item-quantity">
                                x{{ item.quantity }}
                            </div>
                            <div class="order-item-price">
                                {{ (item.product_price / 100).toFixed(2) }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="payment-method-container">
                <div class="underlined-header">
                    Payment Method
                </div>
                <div class="payment-methods">
                    <div class="payment-method-item">
                        <input type="checkbox" name="" id="">
                        <div class="payment-method-logo">
                        </div>
                    </div>
                </div>
            </div>
            <div class="order-summary-container">
                <div class="underlined-header">
                    Order Summary
                </div>
                <div class="order-summary-item">
                    <div>
                        Subtotal:
                    </div>
                    <div class="order-summary-item-amount">
                        {{ (subtotal / 100).toFixed(2) }}
                    </div>
                </div>
                <div class="order-summary-item">
                    <div>
                        Shipping Fee:
                    </div>
                    <div class="order-summary-item-amount">
                        49.00
                    </div>
                </div>
                <div class="order-grand-total">
                    <div>
                        Grand Total:
                    </div>
                    <div class="order-grand-total-amount">
                        {{ ((subtotal + 4900) / 100).toFixed(2) }}
                    </div>
                </div>
            </div>
            <div class="payment-button">
                <button @click="place_order">CONTINUE TO PAYMENT</button>
            </div>
        </div>
    </div>
    <FooterComponent />
</template>

<script>
import FooterComponent from '@/components/common/FooterComponent.vue';
import MarketplaceHeaderComponent from '@/components/common/MarketplaceHeaderComponent.vue';
import MarketplaceSearchComponent from '@/components/common/MarketplaceSearchComponent.vue';
import { checkout, get_pre_checkout } from '@/api/api';

export default {
    name: 'CheckoutPage',
    data() {
        return {
            products: null
        }
    },
    components: {
        FooterComponent,
        MarketplaceHeaderComponent,
        MarketplaceSearchComponent
    },
    methods: {
        place_order: async function () {
            checkout(this.$route.params.id, 49, 1, 'Delivery location', localStorage.getItem('session_id')).then((response) => {
                if (response.status === 0) {
                    this.$router.replace({name: 'SuccessPage'});
                }
            });
        }
    },
    async created() {
        await get_pre_checkout(this.$route.params.id).then((response) => {
            if (response.status === 0) {
                this.products = response.products;
            }
        });
    },
    computed: {
        subtotal: function () {
            let ret = 0;
            if (this.products == null) {
                return ret;
            }
            for (const item of Object.entries(this.products)) {
                ret += item[1].product_price * item[1].quantity;
            }
            return ret;
        }
    }
}
</script>

