<template>
    <MarketplaceHeaderComponent />
    <MarketplaceSearchComponent />
    <div class="product-detail-page-container">
        <div class="generic-container-0">
            <!-- Listings /Papers/Old Books -->
        </div>
        <div class="generic-container-1">
            <div class="product-gallery-container">
                <div class="gallery-current-image">
                    <!-- <img src="../assets/books.jpg" alt=""> -->
                </div>
                <div class="gallery-images-container">
                    <div class="gallery-image">
                        <!-- <img src="../assets/books.jpg" alt=""> -->
                    </div>
                    <div class="gallery-image">
                        <!-- <img src="../assets/books.jpg" alt=""> -->
                    </div>
                    <div class="gallery-image">
                        <!-- <img src="../assets/books.jpg" alt=""> -->
                    </div>
                </div>
            </div>
            <div class="generic-container-3">
                <div class="product-general-info-container">
                    <div class="product-name">
                        <!-- OLD BOOKS -->
                        {{ product.product_name }}
                    </div>
                    <div class="product-price">
                        <!-- 20.00 -->
                        {{ (product.product_price / 100).toFixed(2) }}
                    </div>
                    <div class="product-specific-info-container">
                        <div>
                            Description:
                        </div>
                        <div class="product-info">
                            <!-- old books -->
                            {{ product.product_description }}
                        </div>
                    </div>
                    <div class="product-specific-info-container">
                        <div>
                            Posted:
                        </div>
                        <div class="product-info">
                            <!-- 2 days ago -->
                            {{ product.listed_timestamp }}
                        </div>
                    </div>
                    <div class="product-specific-info-container">
                        <div>
                            No. of Stocks:
                        </div>
                        <div class="product-info">
                            <!-- 1 -->
                            {{ product.product_stock }}
                        </div>
                    </div>
                    <div class="product-specific-info-container">
                        <div>
                            Weight:
                        </div>
                        <div class="product-info">
                            <!-- 500g -->
                            {{ product.product_weight }}
                        </div>
                    </div>
                    <div class="product-specific-info-container">
                        <div>
                            Category:
                        </div>
                        <div class="product-info">
                            <!-- Papers -->
                            {{ product.product_category }}
                        </div>
                    </div>
                    <div class="product-specific-info-container">
                        <div>
                            Location:
                        </div>
                        <div class="product-info">
                            <!-- Sta. Mesa, Manila -->
                            {{ product.product_location }}
                        </div>
                    </div>
                </div>
                <div class="product-actions-container">
                    <button @click="add_to_cart_handler">ADD TO CART</button>
                    <button @click="buy_now">BUY NOW</button>
                </div>
            </div>
        </div>
        <div class="generic-container-2">
            <div class="generic-container-4">
                Seller Information
            </div>
            <div class="generic-container-5">
                <div class="seller-picture">
                    <!-- <img src="../assets/catriona.jpg" alt=""> -->
                </div>
                <div class="seller-identity-container">
                    <div class="seller-displayname">
                        Cat Gray
                    </div>
                    <div class="seller-username">
                        @catslayer
                    </div>
                </div>
            </div>
        </div>
    </div>
    <FooterComponent />
</template>

<script>
import FooterComponent from '@/components/common/FooterComponent.vue';
import MarketplaceHeaderComponent from '@/components/common/MarketplaceHeaderComponent.vue';
import MarketplaceSearchComponent from '@/components/common/MarketplaceSearchComponent.vue';
import { add_to_cart, get_product_with_id } from '@/api/api';

export default {
    name: 'ProductDetailPage',
    components: {
        FooterComponent,
        MarketplaceHeaderComponent,
        MarketplaceSearchComponent
    },
    data() {
        return {
            product: {}
        };
    },
    methods: {
        add_to_cart_handler: function () {
            add_to_cart(this.$route.params.product_id, localStorage.getItem('session_id')).then((response) => {
                if (response.status === 0) {
                    alert('Item added to cart');
                }
            });
        },
        buy_now: function () {
            this.$router.push({ name: 'CheckoutPage' });
        }
    },
    created() {
        get_product_with_id(this.$route.params.product_id).then((response) => {
            this.product = response;
        });
    }
}
</script>

