<template>
    <div class="sign-up-container">
        <div class="logo-container">
            <div class="top-container">
                <img src="@/assets/greencycle-logo.png" alt="">
                <div>SIGN UP</div>
            </div>
            <div class="middle-container">
                <img src="@/assets/greencycle-logo.png" alt="">
            </div>
            <div class="bottom-container">
                <div class="text">
                    where waste finds purpose<br>
                    and sustainability takes flight
                </div>
            </div>
        </div>
        <div class="input-container">
            <div class="input-subcontainer">
                <div class="label-container">
                    <div class="label">Sign Up</div>
                </div>
                <div class="input">
                    <img src="@/assets/user-email-icon.png" alt="">
                    <input ref="input_email" type="text" placeholder="Email">
                </div>
                <div class="input">
                    <img src="@/assets/username-icon.png" alt="">
                    <input ref="input_username" type="text" placeholder="Username">
                </div>
                <div ref="password_alert" class="password-alert"></div>
                <div class="input">
                    <img src="@/assets/password-icon.png" alt="">
                    <input ref="input_password" type="password" placeholder="Password">
                    <div style='display: flex; gap: 1rem;'>
                        <input ref="show_password_checkbox" @click="show_password" type="checkbox" id="show_password">
                        <label for="show_password">Show Password?</label>
                    </div>
                </div>
                <div ref="password_confirm_alert" class="password-alert"></div>
                <div class="input">
                    <img src="@/assets/password-icon.png" alt="">
                    <input ref="input_password_confirm" type="password" placeholder="Confirm your password">
                </div>
                <!-- <div class="radio-container">
                        <span class="sign-up-as">Sign up as</span>
                        <input type="radio" name="sign-up-as" id="buyer-radio">
                        <label for="buyer-radio">Buyer</label>
                        <input type="radio" name="sign-up-as" id="seller-radio">
                        <label for="seller-radio">Seller</label>
                    </div> -->
                <div class="agreement-container">
                    <input ref="agree_checkbox" type="checkbox" name="agreement" id="agreement">
                    <label ref="agreement" for="agreement"> 
                        I agree to the <router-link :to="{name:'TermsOfUsePage'}">Terms and Conditions</router-link><br>and <router-link :to="{name:'PrivacyPolicyPage'}">Data Privacy
                        Policy.</router-link> </label>
                    
                </div>
                <div class="submit-container">
                    <input @click="submit" class="submit-button" type="submit" value="SIGN UP">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { sign_up } from '@/api/api';
export default {
    name: 'SignUp',
    components: {
    },
    methods: {
        show_password: function () {
            if (this.$refs.show_password_checkbox.checked) {
                this.$refs.input_password.type = 'text';
                this.$refs.input_password_confirm.type = 'text';
            } else {
                this.$refs.input_password.type = 'password';
                this.$refs.input_password_confirm.type = 'password';
            }
        },
        submit: function () {
            this.$refs.agree_checkbox.style.boxShadow = '';
            this.$refs.agreement.style.textShadow = '';
            this.$refs.input_password.style.boxShadow = '';
            this.$refs.input_password_confirm.style.boxShadow = '';
            this.$refs.password_alert.textContent = '';
            this.$refs.password_confirm_alert.textContent = '';

            if (!this.$refs.agree_checkbox.checked) {
                this.$refs.agree_checkbox.style.boxShadow = '0px 0px 0.25rem crimson';
                this.$refs.agreement.style.textShadow = '0px 0px 0.25rem crimson';
            }
            if (this.$refs.input_password.value === '') {
                this.$refs.input_password.style.boxShadow = '0px 0px 0.25rem crimson';
                this.$refs.password_alert.textContent = 'Password must not be empty';
            }
            if (this.$refs.input_password_confirm.value === '') {
                this.$refs.input_password_confirm.style.boxShadow = '0px 0px 0.25rem crimson';
                this.$refs.password_confirm_alert.textContent = 'Password must not be empty';
            }
            if (this.$refs.input_password.value !== this.$refs.input_password_confirm.value && (this.$refs.input_password.value !== '') && (this.$refs.input_password_confirm.value !== '')) {
                this.$refs.input_password.style.boxShadow = '0px 0px 0.25rem crimson';
                this.$refs.input_password_confirm.style.boxShadow = '0px 0px 0.25rem crimson';
                this.$refs.password_alert.textContent = 'Password does not match';
                this.$refs.password_confirm_alert.textContent = 'Password does not match';
            }
            if (!this.$refs.agree_checkbox.checked || (this.$refs.input_password.value === '') || (this.$refs.input_password_confirm.value === '') || (this.$refs.input_password.value !== this.$refs.input_password_confirm.value)) {
                return;
            }
            sign_up(this.$refs.input_email.value, this.$refs.input_username.value, this.$refs.input_password.value).then((response) => {
                if (response.status === 0) {
                    alert('A confirmation email will be sent to your address');
                    this.$router.replace({name: 'HomePage'});
                } else {
                    alert(response.msg);
                }
            });
        }
    }
}
</script>

<style scoped>
.password-alert {
    color: crimson;
    line-height: 0;
    margin-left: 3.5rem;
}

.sign-up-container {
    display: flex;
    position: relative;
    isolation: isolate;
    padding-bottom: 4rem;
    align-items: center;
}

.sign-up-container::before {
    content: '';
    background-image: url("@/assets/tree-bg.jpg");
    background-size: cover;
    position: absolute;
    inset: 0;
    z-index: -1;
    opacity: 0.5;
}

.logo-container {
    width: 50%;
}

.top-container {
    display: flex;
    align-items: center;
    color: #3C5E55;
    font-family: Inter;
    font-size: 2rem;
    font-style: normal;
    font-weight: 800;
}

.top-container img {
    height: 9rem;
}

.middle-container {
    text-align: center;
}

.middle-container img {
    height: 22rem;
}

.bottom-container {
    text-align: center;
}

.bottom-container .text {
    color: white;
    font-family: Inter;
    font-size: 1.5rem;
    font-style: italic;
    font-weight: 800;
    text-shadow:
        -2px 2px 1px #3C5E55,
        2px 2px 1px #3C5E55,
        2px -2px 1px #3C5E55,
        -2px -2px 1px #3C5E55,
        -2px 0px 1px #3C5E55,
        2px 0px 1px #3C5E55,
        0px 2px 1px #3C5E55,
        0px 2px 1px #3C5E55;
}

.input-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    padding: 1rem
}

.input-subcontainer {
    background-color: #F1F1F1;
    padding: 2rem;
    border-radius: 1rem;
}

.label-container {
    width: 100%;
    text-align: start;
    padding-bottom: 1rem;
}

.input-subcontainer .label {
    justify-self: right;
    color: #000;
    font-family: Inter;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 800;
}

.input-subcontainer .input {
    display: flex;
    align-items: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

.input-subcontainer .input img {
    height: 2.5rem;
}

.input-subcontainer .input input {
    margin-left: 1rem;
    font-size: 2rem;
    width: 100%;
}

.radio-container {
    text-align: center;
}

.radio-container>* {
    color: #000;
    font-family: Inter;
    font-size: 1rem;
    font-style: italic;
    font-weight: 500;
    padding-right: 1rem;
}

.radio-container .sign-up-as {
    font-weight: 800;
}

.agreement-container {
    color: #000;
    font-family: Inter;
    font-size: 1rem;
    font-style: italic;
    font-weight: 500;
    text-align: center;
}

.submit-container {
    text-align: center;
}

.submit-button:hover{
  transform: scale(1.03);
  transition: transform 0.1s ease-in-out;
}

.submit-container .submit-button {
    background-color: #86A37C;
    border: none;
    border-radius: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 3rem;
    padding-right: 3rem;
    color: #FFF;
    cursor: pointer;
    text-align: center;
    font-family: Inter;
    font-size: 2rem;
    font-style: normal;
    font-weight: 800;
}
</style>