<template>
    <div class="">
        <div class="login-container">
            <div class="generic-container-0">
                <div class="header">
                    <div class="header-logo">
                        <img src="@/assets/greencycle-logo.png" alt="">
                    </div>
                    <div class="header-text">
                        FORGOT PASSWORD
                    </div>
                </div>
                <div class="main-logo center">
                    <img src="@/assets/greencycle-logo.png" alt="">
                </div>
                <div class="main-text center">
                    where waste finds purpose,<br>
                    and sustainability takes flight
                </div>
            </div>
            <div class="generic-container-1">
                <div class="form-container">
                    <div class="form-field">
                        <img src="@/assets/user-email-icon.png" alt="">
                        <input @keyup.enter="submit" ref="form_email" type="text" placeholder="Email">
                    </div>
                    <div class="form-submit center">
                        <button @click="submit">SUBMIT</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { forgot_password } from '@/api/api';
export default {
    name: 'ForgotPassword',
    components: {
    },
    methods: {
        submit: function () {
            forgot_password(this.$refs.form_email.value).then((response) => {
                if (response.status === 0) {
                    alert('Password reset link will be sent to your address');
                    this.$router.replace({ name: 'HomePage' });
                } else if (response.status === -1) {
                    alert('Invalid email');
                    return;
                }
            });
        }
    }
}
</script>

