<template>
    <div class="">
        <MarketplaceHeaderComponent v-if='logged_in' />
        <HeaderComponent v-if='!logged_in' />
        <MarketplaceSearchComponent @search-results="handleSearchResults" />
        <div class="item-list">
            <div v-if="noProductsFound" class="no-product"> No Product Found, try another keyword! </div>
            <ItemCardComponent v-for='product in products' :key='product.product_id' :id='product.product_id' :name='product.product_name' :price='product.product_price'/>
        </div>
        <SlideshowComponent/>
        <CounterRecycedUpcycledComponent/>
        <FooterComponent />
    </div>
</template>

<script>
import MarketplaceSearchComponent from '@/components/common/MarketplaceSearchComponent.vue';
import ItemCardComponent from '@/components/Product/ItemCardComponent.vue';
import SlideshowComponent from '@/components/common/SlideshowComponent.vue';
import CounterRecycedUpcycledComponent from '@/components/common/CounterRecycledUpcycledComponent.vue';
import FooterComponent from '@/components/common/FooterComponent.vue';
import MarketplaceHeaderComponent from '@/components/common/MarketplaceHeaderComponent.vue';
import HeaderComponent from '@/components/common/HeaderComponent.vue';
import { get_products, get_session_user } from '@/api/api';

export default {
    name: 'HomePage',
    components: {
        MarketplaceSearchComponent,
        ItemCardComponent,
        FooterComponent,
        MarketplaceHeaderComponent,
        HeaderComponent,
        CounterRecycedUpcycledComponent,
        SlideshowComponent
    },
    data() {
        return {
            logged_in: false,
            products: [],
            noProductsFound: false
        };
    },
    created() {
        get_session_user(localStorage.getItem('session_id')).then((response) => {
            if (response.status === 0) {
                this.logged_in = true;
            }
        });
        get_products().then((response) => {
            if (response.length !== 0) {
                this.products = response;
            }
        });
    },
    methods:{
        handleSearchResults(searchResults){
            if (searchResults || searchResults.length === 0){
                this.noProductsFound = true;
                setTimeout(() =>{
                    if (this.noProductsFound){
                        window.alert('No Product Found! try another keyword.');
                    }
                }, 0);
                } else {
                    this.products = searchResults;
                  
                }
          
            }
        }
    
}
</script>

<style scoped>
.item-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: 30rem;
    gap: 2rem;
    padding: 1rem;
}

.no-product{
    color: red;

font-family: Inter;
font-size: 60px;
font-style: normal;
font-weight: 800;
line-height: normal;
}
</style>