<template>
    <div class="modal-overlay">
        <div class="modal">
            <div class="modal-header">
                <h2>Edit Product</h2>
                <button class="close-button" @click="$emit('closeModal')">&times;</button>
            </div>
            <div class="modal-body">
                <form @submit.prevent="updateProduct">
                    <div class="form-group">
                        <label for="productName">Product Name</label>
                        <input type="text" id="productName" v-model="product.name">
                    </div>

                    <div class="form-group">
                        <label for="productDescription">Product Description</label>
                        <input type="text" id="productDescription" v-model="product.description">
                    </div>

                    <div class="form-group">
                        <label for="productNoOfStocks">Number of Stocks</label>
                        <input type="text" id="productNoOfStocks" v-model="product.noofstocks">
                    </div>

                    <div class="form-group">
                        <label for="category">Category</label>
                        <select id="category" v-model="product.category">
                            <option disabled value="">Please select one</option>
                            <option value="1">Industrial Waste</option>
                            <option value="2">Commercial waste</option>
                            <option value="3">Domestic waste</option>
                            <option value="4">Agricultural waste</option>
                            <option value="5">Chemical waste</option>
                            <!-- Other categories... -->
                        </select>
                    </div>

                    <div class="form-row">
                        <div class="form-group">
                            <label for="price">Price</label>
                            <input type="text" id="price" v-model="product.price">
                        </div>
                        <div class="form-group">
                            <label for="date">Date</label>
                            <input type="date" id="date" v-model="product.date">
                        </div>
                    </div>

                    <div class="form-group">
                        <label>Upload Images</label>
                        <div class="image-upload-container">
                            <!-- Here you would list your images with v-for and handle file uploads -->
                            <div class="image-upload">
                                <button class="upload-button">+</button>
                            </div>
                        </div>
                    </div>

                    <div class="form-actions">
                        <button type="button" class="cancel-button" @click="$emit('closeModal')">Cancel</button>
                        <button type="submit" class="publish-button" @click="$emit('closeModal')">Publish</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>
  
<script>
import { get_product_details, update_product } from '@/api/api';

export default {
    props: ['productId'], // Expecting a productId prop to be passed in
    data() {
        return {
            product: {
                name: '',
                description: '',
                noofstocks: '',
                category: '',
                price: '',
                date: '',
                images: [] // Assuming images are handled similarly to the other fields
            },
            isLoading: false, // To handle loading state
            errorMessage: '' // To display an error message if something goes wrong
        };
    },
    created() {
        this.fetchProductData();
    },
    methods: {
        async fetchProductData() {
            this.isLoading = true;
            try {
                // Assuming get_product_details is implemented and works as expected
                const response = await get_product_details(this.productId);
                this.product = response.data; // Assign the retrieved data to the product object
            } catch (error) {
                this.errorMessage = 'Failed to load product data.';
                console.error(error);
            } finally {
                this.isLoading = false;
            }
        },
        async updateProduct() {
            this.isLoading = true;
            try {
                const response = await update_product(this.productId, this.product);
                if (response.status === 0) {
                    this.$emit('updateSuccess'); // Emit an event for success
                } else {
                    this.errorMessage = 'Failed to update the product.';
                }
            } catch (error) {
                this.errorMessage = 'An error occurred during the update.';
                console.error(error);
            } finally {
                this.isLoading = false;
            }
        }
    }
};
</script>



<style scoped>
/* Modal Overlay */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

/* Modal Box */
.modal {
    background: white;
    width: 500px;
    border-radius: 12px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/* Modal Header */
.modal-header {
    padding: 1rem;
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal-header h2 {
    font-size: 1.25rem;
    margin: 0;
}

.close-button {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
}

/* Modal Body */
.modal-body {
    padding: 1rem;
}

/* Form Groups */
.form-group {
    margin-bottom: 1rem;
}

.form-group label {
    display: block;
    margin-bottom: 0.5rem;
}

.form-group input[type="text"],
.form-group select,
.form-group input[type="date"] {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
}

/* Image Upload */
.image-upload-container {
    display: flex;
    gap: 0.5rem;
}

.image-upload {
    width: 60px;
    height: 60px;
    border: 1px dashed #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: #ccc;
    cursor: pointer;
}

/* Form Actions */
.form-actions {
    display: flex;
    justify-content: flex-end;
    padding-top: 1rem;
}

.cancel-button,
.publish-button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.cancel-button {
    background-color: #f5f5f5;
    margin-right: 0.5rem;
}

.publish-button {
    background-color: #4F7959;
    color: white;
}

.form-row {
    display: flex;
    gap: 1rem;
}

/* Responsive adjustments can be placed here */
@media (max-width: 600px) {
    .modal {
        width: 90%;
    }

    .form-row {
        flex-direction: column;
    }
}
</style>
  