<template>
    <div>
        <div class="component-window">
            <div class="search-bar">
                <input class="search" type="text" placeholder="Search...">
                <button type="submit" class="search-btn">
                    <i class="fa fa-search"></i>
                </button>
            </div>
            <div class="dropdown">
                <button class="dropbtn" @click="toggleDropdown">{{ selectedOption || 'User Type' }} &#9660;</button>
                <div class="dropdown-content" :class="{ show: isDropdownOpen }">
                    <a @click="selectOption('Buyer')">Buyer</a>
                    <a @click="selectOption('Seller')">Seller</a>
                </div>
            </div>
            <div class="date-range-picker">
                <input type="date" ref="start_date">
                <span>to</span>
                <input type="date" ref="end_date">
            </div>
            <div class="table-container">
                <table>
                    <thead>
                        <tr>
                            <th>User ID</th>
                            <th>User Type</th>
                            <th>Username</th>
                            <th>User Email</th>
                            <th>Date & Time</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>115</td>
                            <td>Buyer</td>
                            <td>Papa P</td>
                            <td>piolopascual@gmail.com</td>
                            <td>2023-09-05 10:44 AM</td>
                            <td>Sign in</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'AdminAuditTrailComponent',
    components: {
    },
    data() {
        return {
            isDropdownOpen: false,
            selectedOption: null,
        };
    },
    methods: {
        toggleDropdown() {
            this.isDropdownOpen = !this.isDropdownOpen;
        },
        selectOption(option) {
            this.selectedOption = option;
            this.isDropdownOpen = false; // Close the dropdown after selection
        },
        showCalendar() {
            // Handle calendar display logic here.
            alert('Calendar button clicked!');
        },
    },
    computed: {
        buttonText() {
            return this.selectedOption || 'Dropdown';
        },
    },
    created() {
        document.addEventListener('DOMContentLoaded', () => {
            const startDate = this.$refs.start_date;
            const endDate = this.$refs.end_date;

            startDate.addEventListener('change', () => {
                if (new Date(startDate.value) > new Date(endDate.value)) {
                    endDate.value = startDate.value;
                }
            });

            endDate.addEventListener('change', () => {
                if (new Date(endDate.value) < new Date(startDate.value)) {
                    startDate.value = endDate.value;
                }
            });
        });
    }
}
</script>

