    color: #3C5E55;
    cursor: pointer;
    font-size: 20px;
}

.dropdown {
    display: inline-block;
    margin: 1rem 1rem 0rem 1rem;
}

.dropbtn {
    width: 95px;
    background-color: #BBBBBB;
    color: black;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    font-weight: bold;
    font-family: Inter;
    font-size: 12px;
}

/* Dropdown content (hidden by default) */
.dropdown-content {
    display: none;
