<template>
    <div class="page-selector-container">
        <div class="pages">
            <div v-if="pages > 1" class="page navigator">&lt;</div>
            <!-- <div v-if="current_page > 3 && pages > 5" @click="handler(1)" class="page page-number">1</div> -->
            <!-- <div v-if="current_page > 4 && pages > 6" class="page-number">...</div> -->
            <div v-if="pages > 0" :ref="(el) => page_buttons[0] = el" @click="handler(display_page)" class="page page-number">{{ display_page }}</div>
            <div v-if="pages > 1" :ref="(el) => page_buttons[1] = el" @click="handler(display_page + 1)" class="page page-number">{{ display_page + 1 }}</div>
            <div v-if="pages > 2" :ref="(el) => page_buttons[2] = el" @click="handler(display_page + 2)" class="page page-number">{{ display_page + 2 }}</div>
            <div v-if="pages > 3" :ref="(el) => page_buttons[3] = el" @click="handler(display_page + 3)" class="page page-number">{{ display_page + 3 }}</div>
            <div v-if="pages > 4" :ref="(el) => page_buttons[4] = el" @click="handler(display_page + 4)" class="page page-number">{{ display_page + 4 }}</div>
            <div v-if="pages > 5 && ((display_page + 4) < pages)" class="page-number">...</div>
            <div v-if="pages > 1" class="page navigator">&gt;</div>
        </div>
    </div>
    <div class="page-selector-container">
        <div class="pages">
            <div v-for="page in pages" :key="page" @click="handler(page)" class="page page-number">{{ page }}</div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'PageSelectorComponent',
    data() {
        return {
            prev_page: 0,
            page_buttons: [],
            current_page: 1,
            display_page: 1,
            pages: 20
        }
    },
    components: {
    },
    methods: {
        handler: function (page_num) {
            if (page_num === this.current_page) {
                return;
            }
            this.page_buttons[this.current_page - this.display_page].classList.remove('selected');
            if ((page_num - this.display_page) > 2) {
                if ((this.display_page + 5) < this.pages && page_num < (this.pages - 2)) {
                    this.display_page += (page_num - this.display_page) - 2;
                } else {
                    this.display_page = this.pages - 4;
                }
            } else if ((this.display_page + 2 - page_num) > 0) {
                if (this.display_page > 3 && page_num > 2) {
                    this.display_page -= (this.display_page + 2 - page_num);
                } else {
                    this.display_page = 1;
                }
            }
            this.page_buttons[page_num - this.display_page].classList.add('selected');
            this.current_page = page_num;
        }
    },
    mounted() {
        this.page_buttons[0].classList.add('selected');
    }
}
</script>

<style scoped>
.navigator {
    color: #A6B697;
    font-size: 1.5rem;
    margin-inline: 0.25rem;
    padding-inline: 1rem;
    margin-inline: 2rem;
}

.page {
    border-radius: 0.2rem;
    outline: solid lightgrey 1px;
}

.pages {
    display: inline-flex;
}

.page-number {
    color: #A6B697;
    font-size: 1.5rem;
    margin-inline: 0.25rem;
    padding-inline: 1rem;
}

.page-selector-container {
    padding-block: 2rem;
    text-align: center;
}

.selected {
    background-color: #A6B697;
    color: white;
}</style>