            <div class="search-input">
                <input v-model="searchTerm" type="text" placeholder="What are you looking for?" @keyup.enter="search">
                <img @click="search" src="@/assets/search-icon.png" alt="Search">
            </div>
            </form>
            <div class="tags-container">
