<script>

import {search_product} from '@/api/api';
export default {
    name: 'MarketplaceSearchComponent',
    data(){
        return{
            searchTerm: '',
        };
    },
    components: {
    },
    methods: {
        async search(event) {
            event.preventDefault();
            console.log('Searching for:', this.searchTerm);
            try{
                const response = await search_product(this.searchTerm);
                console.log('Search results:', response);
                this.$emit('search-results', response);
            } catch(error){
                console.error('Error fetching search results:', error);
            }
         
        }
    },
   
}
</script>

