<template>
    <AddPostComponent v-if="showModal" @close-modal="showModal = false" />
    <MarketplaceHeaderComponent />
    <div class="btn-container">
        <button @click="showModal = true" class="create-btn">
            Create post +
        </button>
    </div>
    <div class="newsfeed-container">
        <NewsfeedCardComponent v-for="(post, key) in newsfeed" :key='key' :post="post"
        @favorite-toggled="handFavoriteToggled" />
    </div>
    <PageSelectorComponent />
    <FooterComponent />
</template>

<script>
import FooterComponent from '@/components/common/FooterComponent.vue';
import MarketplaceHeaderComponent from '@/components/common/MarketplaceHeaderComponent.vue';
import NewsfeedCardComponent from '@/components/NewsfeedCardComponent.vue';
import PageSelectorComponent from '@/components/common/PageSelectorComponent.vue';
import AddPostComponent from '@/components/AddPostComponent.vue';
import { get_post, get_session_user } from '@/api/api';

export default {
    name: 'NewsfeedRon',
    data() {
        return {
            showModal: false,
            logged_in: false,
            favoritePost: [],
            newsfeed: {}
        }
    },
    components: {
        NewsfeedCardComponent,
        MarketplaceHeaderComponent,
        FooterComponent,
        PageSelectorComponent,
        AddPostComponent
    },
    methods:{
        handleFavoriteToggled(post, isFavorite){
            if (isFavorite){
                this.favoritePost.push(post);
            } else{
                const index = this.favoritePost.findIndex((p) => p.id === post.id);
                if (index !== -1)
                {
                    this.favoritePost.splice(index, );
                }
            }
        }
    },
    created() {
        get_session_user(localStorage.getItem('session_id')).then((response) => {
            if (response.status === 0) {
                this.logged_in = true;
            }
        });
        get_post().then((response) => {
            this.newsfeed = response;
        });
    },
}
</script>

<style scoped>
.btn-container {
    text-align: end;
}

.create-btn {
    display: inline-block;
    background-color: #A6B697;
    border-radius: 1.5rem;
    margin-block: 1rem;
    padding: 1rem 1rem 1rem 1rem;
}

.create-btn:hover {
    transform: scale(1.09);
    transition: transform 0.1s ease-in-out;
}

.newsfeed-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
}
</style>