<template>
    <div>
        <div class="component-window">
            <div class="search-bar">
                <input class="search" type="text" placeholder="Search...">
                <button type="submit" class="search-btn">
                    <i class="fa fa-search"></i>
                </button>
            </div>
            <div class="dropdown">
                <button class="dropbtn" @click="toggleDropdown">{{ selectedOption || 'User Type' }} &#9660;</button>
                <div class="dropdown-content" :class="{ show: isDropdownOpen }">
                    <a @click="selectOption('Buyer')">Buyer</a>
                    <a @click="selectOption('Seller')">Seller</a>
                </div>
            </div>
            <div>
                <table>
                    <thead>
                        <tr>
                            <th>User ID</th>
                            <th>User Type</th>
                            <th>User Email</th>
                            <th>Location</th>
                            <th>Last Online</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        <UserCardComponent v-for="value, key in users" :key="key" :user_key="key" :user_info="value"
                            @archive="(user_key) => archive_handler(user_key)"
                            @view="(user_key) => view_handler(user_key)" />
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
import UserCardComponent from './UserCardComponent.vue';
import { archive_user, get_users } from '@/api/api';

export default {
    name: 'AdminUsersComponent',
    components: {
        UserCardComponent
    },
    data() {
        return {
            isDropdownOpen: false,
            selectedOption: null,
            users: {}
        };
    },
    methods: {
        toggleDropdown: function () {
            this.isDropdownOpen = !this.isDropdownOpen;
        },
        selectOption: function (option) {
            this.selectedOption = option;
            this.isDropdownOpen = false; // Close the dropdown after selection
        },
        archive_handler: function (user_key) {
            console.log('archive: ' + user_key);
            archive_user(this.users[user_key].user_id).then((response) => {
                if (response.status === 0) {
                    delete this.users[user_key];
                }
            });
        },
        view_handler: function (user_key) {
            console.log('view: ' + user_key);
        }
    },
    computed: {
        buttonText: function () {
            return this.selectedOption || 'Dropdown';
        }
    },
    created() {
        get_users().then((response) => {
            for (const [user_key, user_info] of Object.entries(response)) {
                this.users[user_key] = user_info;
            }
        })
    }
}
</script>

<style scoped>
.component-window {
    background-color: white;
    flex: 1;
    margin: 0.5rem 0rem 0.5rem 0;
    float: left;
    width: 700px;
}

.search-bar {
    margin: 1rem 1rem 0rem 1rem;
    display: inline-block;
}

.search {
    width: 75%;
    border: 2px solid #3C5E55;
    padding: 5px;
    height: 20px;
    border-radius: 5px 5px 5px 5px;
    outline: none;
    color: #9DBFAF;
}

.search:focus {
    color: #3C5E55;
}

.search-btn {
    width: 40px;
    height: 40px;
    border: 1px solid white;
    background: white;
    text-align: center;
    color: #3C5E55;
    cursor: pointer;
    font-size: 20px;
}

.dropdown {
    display: inline-block;
    margin: 1rem 1rem 0rem 1rem;
}

.dropbtn {
    width: 95px;
    background-color: #BBBBBB;
    color: black;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    font-weight: bold;
    font-family: Inter;
    font-size: 12px;
}

/* Dropdown content (hidden by default) */
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 95px;
    z-index: 1;
    font-family: Inter;
    font-size: 12px;
}

/* Show the dropdown content when the dropdown button is clicked */
.show {
    display: block;
}

/* Style the dropdown menu links */
.dropdown-content a {
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

/* Change color on hover */
.dropdown-content a:hover {
    background-color: #ddd;
}

.table-container {
    background-color: white;
    flex: 1;
    margin: 0rem 0.5rem 0.5rem 0;
    float: left;
    width: 700px;
}

table {
    border-collapse: collapse;
    width: 95%;
    margin: 1rem 1rem 1rem 1rem;
}

tr:hover {
    background-color: #f5f5f5;
}

th {
    border: 1px solid #ddd;
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #D2D2D2;
    color: black;
    font-size: 10px;
}
</style>