<template>
    <MarketplaceHeaderComponent>Seller</MarketplaceHeaderComponent>
    <div class="seller-dashboard-container">
        <div class="banner">
        </div>
        <div class="profile-picture-container">
            <div class="profile-picture">
            </div>
        </div>
        <div class="profile">
            <div class="profile-info-container">
                <div class="profile-info">
                    <div class="display-name">
                        Cat Gray
                    </div>
                    <div class="username">
                        @catslayer
                    </div>
                    <div class="location">
                        Quezon City
                    </div>
                </div>
            </div>
            <div class="header">
                Seller Dashboard
            </div>
            <div class="menu">
                <div class="menu-selector">
                    <div :ref="(el) => menu_item[0] = el" @click="menu_toggle(0)" class="menu-selector-item">
                        My Profile
                    </div>
                    <div :ref="(el) => menu_item[1] = el" @click="menu_toggle(1)" class="menu-selector-item">
                        My Products
                    </div>
                    <div :ref="(el) => menu_item[2] = el" @click="menu_toggle(2)" class="menu-selector-item">
                        My Orders
                    </div>
                    <div :ref="(el) => menu_item[3] = el" @click="menu_toggle(3)" class="menu-selector-item">
                        Transactions
                    </div>
                </div>
                <SellerProfileComponent v-if="current_menu === 0" />
                <SellerMyOrdersComponent v-if="current_menu === 2" />
                <SellerMyProductsComponent v-if="current_menu === 1" />
                <SellerTransactionsComponent v-if="current_menu === 3" />
            </div>
        </div>
    </div>
</template>

<script>
import MarketplaceHeaderComponent from '@/components/common/MarketplaceHeaderComponent.vue';
import SellerProfileComponent from '@/components/Dashboard/SellerDashboard/SellerProfileComponent.vue';
import SellerMyOrdersComponent from '@/components/Dashboard/SellerDashboard/SellerMyOrdersComponent.vue';
import SellerMyProductsComponent from '@/components/Dashboard/SellerDashboard/SellerMyProductsComponent.vue';
import SellerTransactionsComponent from '@/components/Dashboard/SellerDashboard/SellerTransactionsComponent.vue';

export default {
    name: 'SellerDashboard',
    data() {
        return {
            current_menu: 0,
            editing: false,
            menu_item: []
        }
    },
    components: {
        MarketplaceHeaderComponent,
        SellerProfileComponent,
        SellerMyOrdersComponent,
        SellerMyProductsComponent,
        SellerTransactionsComponent
    },
    methods: {
        menu_toggle: function (menu) {
            if (this.current_menu !== menu) {
                this.menu_item[this.current_menu].style.outline = 'none';
                this.menu_item[menu].style.outline = 'solid';
                this.current_menu = menu;
            }
        }
    },
    mounted() {
        this.menu_item[this.current_menu].style.outline = 'solid';
    }
}
</script>

<style scoped>
