    height: 7.5rem;
}

.profile-picture {
    background: url('@/assets/catriona.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 15rem;
    width: 15rem;
}

.profile-picture-container {
    background-color: rgba(0, 0, 0, 0.5);
    margin-left: 5rem;
