<template>
    <div class="individual-item-container">
        <div class="individual-item-checkbox">
            <input ref="checkbox" @click="(e) => item_select(e)" type="checkbox" name="select" id="">
        </div>
        <div class="individual-item-image-container">
            <img src="@/assets/books.jpg" alt="">
        </div>
        <div class="individual-item-info-container">
            <div class="individual-item-name">
                {{ item_info.product_name }}
            </div>
            <div class="individual-item-price">
                {{ (item_info.product_price / 100).toFixed(2) }}
            </div>
        </div>
        <div class="generic-container-3">
            <div class="item-quantity-modifiers-container">
                <input @click="$emit('subtractQuantity', item_key)"
                    class="item-quantity-subtract-button item-quantity-modifier-button" type="button" value="-">
                <input class="item-quantity-text" type="text" name="" id="" :value="item_info.quantity">
                <input @click="$emit('addQuantity', item_key)"
                    class="item-quantity-add-button item-quantity-modifier-button" type="button" value="+">
                <img @click="$emit('removeItem', item_key)" style="cursor: pointer;" src="@/assets/trash.png" alt="">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'CartItemCardComponent',
    data() {
        return {
            old_select_all: false
        }
    },
    components: {
    },
    props: ['item_key', 'item_info', 'select_all', 'select_state_guard'],
    methods: {
        item_select: function (e) {
            if (e.target.checked) {
                this.$emit('itemSelected', this.item_key);
            } else {
                this.$emit('itemDeselected', this.item_key);
            }
        }
    },
    updated() {
        if (!this.select_state_guard) {
            if (this.select_all != this.old_select_all) {
                if (this.select_all) {
                    this.$refs.checkbox.checked = this.select_all;
                    this.$emit('itemSelected', this.item_key);
                    console.log('itemSelected');
                } else {
                    this.$refs.checkbox.checked = this.select_all;
                    this.$emit('itemDeselected', this.item_key);
                    console.log('itemDeselected');
                }
            }
        }
        this.old_select_all = this.select_all;
    }
}
</script>

<style scoped>
.generic-container-3 {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    flex: 1;
}

.generic-container-3 a {
    display: flex;
    align-items: center;
}

.generic-container-3 img {
    height: 1.5rem;
    width: 1.5rem;
}

.individual-item-container {
    background-color: white;
    display: flex;
    padding: 0.5rem;
}

.individual-item-checkbox {
    display: flex;
    align-items: center;
    /* padding-inline: 1rem; */
}

.individual-item-info-container {
    padding-inline: 0.5rem;
}

.individual-item-image-container {
    max-width: 5rem;
    max-height: 5rem;
    padding-left: 1rem;
}

.individual-item-image-container img {
    object-fit: cover;
    height: 100%;
    width: 100%;
    border: 1px;
    border-style: solid;
}

.individual-item-info-container {
    text-align: start;
}

.individual-item-name {
    font-size: 1.25rem;
}

.individual-item-price {
    font-size: 1.5rem;
    font-weight: bold;
}

.item-quantity-modifiers-container {
    display: flex;
    align-items: center;
}

.item-quantity-modifier-button {
    background-color: #4F7959;
    color: white;
    cursor: pointer;
    height: 1.9rem;
    width: 2rem;
    border: none;
    font-size: 2rem;
    font-family: 'Inter';
    line-height: 0%;
}

.item-quantity-add-button {
    border-radius: 0rem 1rem 1rem 0rem;
}

.item-quantity-text {
    color: white;
    background-color: black;
    height: 1.75rem;
    width: 3rem;
    border: none;
    font-size: 1rem;
    font-family: 'Inter';
    text-align: center;
}

.item-quantity-subtract-button {
    border-radius: 1rem 0rem 0rem 1rem;
}
</style>