<template>
    <div class="modal-overlay">
        <div class="modal">
            <div class="modal-header">
                <h2 v-if="!editingState">Add New Product</h2>
                <h2 v-if="editingState">Edit Product</h2>
                <button class="close-button" @click="$emit('closeModal')">&times;</button>
            </div>
            <div class="modal-body">
                <form @submit.prevent="submitForm">
                    <div class="form-group">
                        <label for="productName">Product Name</label>
                        <input type="text" id="productName" v-model="product.name">
                    </div>
                    <div class="form-group">
                        <label for="productDescription">Product Description</label>
                        <input type="text" id="productDescription" v-model="product.description">
                    </div>

                    <div class="form-group">
                        <label for="productNoOfStocks">Number of Stocks</label>
                        <input type="text" id="productNoOfStocks" v-model="product.stock">
                    </div>

                    <div class="form-group">
                        <label for="category">Category</label>
                        <select id="category" v-model="product.category">
                            <option disabled value="">Please select one</option>
                            <option value="1">Industrial Waste</option>
                            <option value="2">Commercial waste</option>
                            <option value="3">Domestic waste</option>
                            <option value="4">Agricultural waste</option>
                            <option value="5">Chemical waste</option>
                            <!-- Other categories... -->
                        </select>
                    </div>

                    <div class="form-row">
                        <div class="form-group">
                            <label for="price">Price</label>
                            <input type="text" id="price" v-model="product.price">
                        </div>
                    </div>

                    <div class="form-group">
                        <label>Upload Images</label>
                        <div class="image-upload-container">
                            <div class="image-upload">
                                <input type="file" @change="uploadImage">
                            </div>
                            <!-- Display uploaded images -->
                            <div v-if="product.images.length > 0" class="uploaded-images">
                                <div
                                    v-for="(image, index) in product.images"
                                    :key="index"
                                    class="uploaded-image"
                                >
                                    <img :src="image" alt="Uploaded Image" />
                                    <button @click="removeImage(index)" class="remove-image">
                                        <i class="fa fa-times"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="form-actions">
                        <button type="button" class="cancel-button" @click="$emit('closeModal')">Cancel</button>
                        <button type="submit" class="publish-button">Publish</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>
  
<script>
import { add_product, update_product } from '@/api/api';

export default {
    name: 'SellerAddProductComponent',
    props: ['editingState', 'itemEdited'],
    data() {
        return {
            product: {
                name: '',
                description: '',
                stock: '',
                category: '',
                price: '',
                images: [], // Array to store uploaded image URLs
            },
            selectedFile: null, // To store the selected file for upload
        };
    },
    methods: {
        submitForm() {
            if (this.editingState) {
                update_product(
                    this.product.name,
                    this.product.price * 100,
                    this.product.description,
                    this.product.stock,
                    0,
                    this.product.category,
                    'new location',
                    this.itemEdited.product_id,
                    this.itemEdited.is_pending,
                    localStorage.getItem('session_id')
                ).then((response) => {
                    console.log('update_product: ', response);
                    if (response.status === 0) {
                        this.$emit('success');
                    }
                });
            } else {
                add_product(
                    this.product.name,
                    this.product.price * 100,
                    this.product.description,
                    this.product.stock,
                    this.product.category,
                    localStorage.getItem('session_id')
                ).then((response) => {
                    console.log('add_product ', response);
                    if (response.status === 0) {
                        this.$emit('success');
                    }
                });
            }
        },
        // Method to handle image upload
        onFileChange(event) {
            this.selectedFile = event.target.files[0];
        },
        // Method to upload an image
        async uploadImage() {
            if (!this.selectedFile) {
                return;
            }

            const formData = new FormData();
            formData.append('file', this.selectedFile);

            // Make the image upload request
            try {
                const response = await fetch('http://localhost:8080/upload', {
                method: 'POST',
                body: formData,
                });

                if (response.ok) {
                const data = await response.json();
                const imageUrl = data.imageUrl;

                // Add the uploaded image URL to the product's images array
                this.product.images.push(imageUrl);

                // Clear the selected file
                this.selectedFile = null;

                } else {
                console.error('Image upload failed.');
                }
            } catch (error) {
                console.error('Image upload error:', error);
            }
        },
        /*// Method to remove an uploaded image
        removeImage(index) {
            this.product.images.splice(index, 1);
        },*/
        created() {
            if (this.editingState) {
                this.product.name = this.itemEdited.product_name;
                this.product.description = this.itemEdited.product_description;
                this.product.stock = this.itemEdited.product_stock;
                this.product.category = this.itemEdited.product_category;
                this.product.price = (this.itemEdited.product_price / 100).toFixed(2);
            }
        }
    }
};
</script>

<style scoped>
/* Modal Overlay */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

/* Modal Box */
.modal {
    background: white;
    width: 500px;
    border-radius: 12px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/* Modal Header */
.modal-header {
    padding: 1rem;
    border-bottom: 1px solid #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal-header h2 {
    font-size: 1.25rem;
    margin: 0;
}

.close-button {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
}

/* Modal Body */
.modal-body {
    padding: 1rem;
}

/* Form Groups */
.form-group {
    margin-bottom: 1rem;
}

.form-group label {
    display: block;
    margin-bottom: 0.5rem;
}

.form-group input[type="text"],
.form-group select,
.form-group input[type="date"] {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
}

/* Image Upload */
.image-upload-container {
    display: flex;
    gap: 0.5rem;
}

/*.image-upload {
    width: 60px;
    height: 60px;
    border: 1px dashed #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: #ccc;
    cursor: pointer;
}*/

/* Form Actions */
.form-actions {
    display: flex;
    justify-content: flex-end;
    padding-top: 1rem;
}

.cancel-button,
.publish-button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.cancel-button {
    background-color: #f5f5f5;
    margin-right: 0.5rem;
}

.publish-button {
    background-color: #4F7959;
    color: white;
}

.form-row {
    display: flex;
    gap: 1rem;
}

/* Responsive adjustments can be placed here */
@media (max-width: 600px) {
    .modal {
        width: 90%;
    }

    .form-row {
        flex-direction: column;
    }
}
</style>
  