<template>
    <div class="modal-overlay">
        <div class="modal">
            <div class="modal-header">
                <div class="content-container">
                    <div class="addpost"> Add Post</div>
                    <div class="body">
                        <form id="postForm">
                            <div class="form-group">
                                <label for="photo">Select Photo:</label>
                                <input type="file" id="photo" name="photo">
                            </div>
                            <div class="form-group">
                                <label for="title">Title:</label>
                                <input type="text" id="title" v-model="title" required>
                            </div>
                            <div class="form-group">
                                <label for="description">Description:</label>
                                <textarea id="description" v-model="description" rows="5" required></textarea>
                            </div>
                            <div class="buttons">
                                <button @click="$emit('closeModal')" type="cancel" class="btn">Cancel</button>
                                <button @click="addpost" type="submit" class="btn">Publish</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div> 
    </div>
</template>

<script>
import { add_post } from '@/api/api';
export default {
    name: 'AddPostComponent',
    data() {
        return {
            title: '',
            description: '',
            image: null

        }
    }, 
    components: {
    },
    methods: {
        addpost: function (){
            add_post(this.title, 'hakdog', this.description, localStorage.getItem('session_id')).then((response) => {
                if (response.status === 0) {
                    this.$emit('closeModal');
                } 
            })
            console.log(this.image);
        }
    }

}
</script>

