<template>
    <div class="login-container">
        <div class="generic-container-0">
            <div class="header">
                <div class="header-logo">
                    <img src="@/assets/greencycle-logo.png" alt="">
                </div>
                <div class="header-text">
                    LOG IN
                </div>
            </div>
            <div class="main-logo center">
                <img src="@/assets/greencycle-logo.png" alt="">
            </div>
            <div class="main-text center">
                where waste finds purpose,<br>
                and sustainability takes flight
            </div>
        </div>
        <div class="generic-container-1">
            <div class="form-container">
                <div class="form-field">
                    <img src="@/assets/user-email-icon.png" alt="">
                    <input @keyup.enter="submit" ref="form_username" type="text" placeholder="Username">
                </div>
                <div class="form-field">
                    <img src="@/assets/password-icon.png" alt="">
                    <input @keyup.enter="submit" ref="form_password" type="password" placeholder="Password">
                </div>
                <div style="text-align: end;">
                    <span @click="goto_forgot_password" style="cursor: pointer;text-decoration: underline;">Forgot
                        password</span>
                </div>
                <div class="form-submit center">
                    <button @click="submit">LOG IN</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { get_session_user, login } from '@/api/api';
export default {
    name: 'NewLogin',
    components: {
    },
    created() {
        get_session_user(localStorage.getItem('session_id')).then((response) => {
            if (response.status !== -1) {
                this.$router.replace({ name: 'HomePage' })
            }
        });
    },
    methods: {
        goto_forgot_password: function () {
            this.$router.push({ name: 'ForgotPassword' });
        },
        submit: function () {
            login(this.$refs.form_username.value, this.$refs.form_password.value).then((response) => {
                if (response.status === 0) {
                    get_session_user(response.session_id).then((res) => {
                        if (res.status === 0) {
                            this.greencycle_loggedin.value = 1;
                            this.greencycle_user_id.value = res.user_id;
                            this.greencycle_username.value = res.username;
                            this.greencycle_description.value = res.description;
                            this.greencycle_sessionid.value = response.session_id;
                        }
                    });
                    localStorage.setItem('session_id', response.session_id);
                    if (response.user_type === 0) { // Admin
                        this.$router.push({ name: 'AdminDashboard' });
                    } else if (response.user_type === 1) { // User/Seller
                        this.$router.push({ name: 'SellerDashboard' });
                    } else { // User
                        this.$router.push({ name: 'HomePage' });
                    }
                } else if (response.status === -1) {
                    alert('Username or password incorrect');
                } else if (response.status === -2) {
                    alert('Account not yet confirmed');
                }
            })
        },
        localStorage_test: function () {
            if (this.$refs.form_username.value == '') {
                console.log(localStorage.getItem('greencycle'));
            } else {
                localStorage.setItem('greencycle', this.$refs.form_username.value);
            }
        }
    }
}
</script>

<style scoped>
.center {
    text-align: center;
}

.form-container {
    background-color: #F1F1F1;
    border-radius: 1rem;
    margin: 1rem;
    padding: 1rem;
}

.form-container>* {
    margin: 1rem 0 0 0;
}

.form-field {
    display: flex;
    align-items: center;
}

.form-field img {
    height: 2.5rem;
    margin-right: 1rem;
}

.form-field input {
    font-size: 2rem;
}

.form-submit button {
    background-color: #86A37C;
    border: none;
    border-radius: 2rem;
    color: white;
    cursor: pointer;
    font-family: Inter;
    font-size: 2rem;
    font-weight: 800;
    padding: 0.5rem 3rem 0.5rem 3rem;
    cursor: pointer;
}

.form-submit button:hover {
    animation: pulsate 100s;
}

@keyframes pulsate {
    0% {
        transform: scale(1.09);
    }

    70% {
        transform: scale(1.2);
    }

    100% {
        transform: scale(1);
    }
}

.generic-container-0 {
    display: flex;
    flex: 1;
    flex-direction: column;
}

.generic-container-1 {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header {
    display: flex;
    align-items: center;
}

.header-logo img {
    height: 9rem;
}

.header-text {
    color: #3C5E55;
    font-size: 2rem;
    font-weight: 800;
}

.login-container {
    display: flex;
    font-family: 'Inter';
    isolation: isolate;
    padding-bottom: 10rem;
    position: relative;
}

.login-container::before {
    content: '';
    background-image: url('@/assets/tree-bg.jpg');
    background-size: cover;
    position: absolute;
    inset: 0;
    z-index: -1;
    opacity: 0.5;
}

.main-logo img {
    height: 22rem;
}

.main-text {
    color: white;
    font-size: 1.5rem;
    font-style: italic;
    font-weight: 800;
    text-shadow:
        -2px 2px 1px #3C5E55,
        2px 2px 1px #3C5E55,
        2px -2px 1px #3C5E55,
        -2px -2px 1px #3C5E55,
        -2px 0px 1px #3C5E55,
        2px 0px 1px #3C5E55,
        0px 2px 1px #3C5E55,
        0px 2px 1px #3C5E55;
}
</style>